<template>
  <div name="change-type">
    <expandable-section-std title="Change Type" ref="expandChangeType" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div v-show="emoc.Permanent__c" class="col-md-6">
            <div class="row">
              <div class="col-4">
                <checkbox-std
                  v-model="emoc.Permanent__c"
                  label="Permanent"
                  :inline="true"
                  disabled
                />
              </div>
            </div>
            <div v-if="emoc.Permanent__c" class="row">
              <div class="col-md-6">
                <label class="slds-form-element__label">
                  <abbr class="slds-required" title="required">*</abbr>Required Completion date
                </label>
                <div class="slds-form-element__icon help-text-icon">
                  <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
                    <i class="fa fa-info-circle" aria-hidden="true"></i>
                    <span class="slds-assistive-text">Help</span>
                  </button>
                  <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-display" role="tooltip" id="help" style="position:absolute;top:-50px;left:-15px;width: 175px;">
                    <div class="slds-popover__body">
                      RAM = L approx. 90 days<br>
                      RAM = M approx. 60 days<br>
                      RAM = H approx. 30 days<br>
                    </div>
                  </div>
                </div>
                <v-menu v-model="showPickerComplDate"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="emoc.Completion_date__c"
                      v-validate="'required|date_format:dd/MM/yyyy'"
                      data-vv-as="Completion date"
                      data-vv-value-path="inputValue"
                      name="Completion_date__c"
                      :error="errors.has('Completion_date__c')"
                      @blur="dateCompletion = parseDate(emoc.Completion_date__c)"
                      placeholder="dd/mm/yyyy"
                      v-bind="attrs"
                      v-on="on"
                      append-icon="mdi-calendar"
                      autocomplete="off"
                      hide-details
                      outlined
                      dense
                      disabled
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    no-title
                    v-model="dateCompletion"
                    @change="showPickerComplDate = false"
                    :min="minCalendar"
                  ></v-date-picker>
                </v-menu>
                <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('Completion_date__c') }}</span></div>
              </div>
            </div>
          </div>
          <div v-show="emoc.Temporary__c" class="col-md-6">
            <div class="row">
              <div class="col-4">
                <checkbox-std
                  v-model="emoc.Temporary__c"
                  label="Temporary"
                  :inline="true"
                  disabled
                />
              </div>
            </div>
            <div  v-if="emoc.Temporary__c" class="row">
              <div class="col-md-6">
                <label class="slds-form-element__label">
                  <abbr class="slds-required" title="required">*</abbr>Required Start date
                </label>
                <div class="slds-form-element__icon help-text-icon">
                  <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
                    <i class="fa fa-info-circle" aria-hidden="true"></i>
                    <span class="slds-assistive-text">Help</span>
                  </button>
                  <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-display" role="tooltip" id="help" style="position:absolute;top:-50px;left:-15px;width: 175px;">
                    <div class="slds-popover__body">
                      RAM = L approx. 90 days<br>
                      RAM = M approx. 60 days<br>
                      RAM = H approx. 30 days<br>
                    </div>
                  </div>
                </div>
                <v-menu v-model="showPickerStartDate"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="emoc.Start_date__c"
                      v-validate="'required|date_format:dd/MM/yyyy'"
                      data-vv-as="Start date"
                      data-vv-value-path="inputValue"
                      name="Start_date__c"
                      :error="errors.has('Start_date__c')"
                      @blur="dateStart = parseDate(emoc.Start_date__c)"
                      placeholder="dd/mm/yyyy"
                      v-bind="attrs"
                      v-on="on"
                      append-icon="mdi-calendar"
                      autocomplete="off"
                      hide-details
                      outlined
                      dense
                      disabled
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    no-title
                    v-model="dateStart"
                    @change="showPickerStartDate = false"
                    :min="minCalendar"
                  ></v-date-picker>
                </v-menu>
                <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('Start_date__c') }}</span></div>
              </div>
              <div class="col-md-6">
                <label class="slds-form-element__label">
                  <abbr class="slds-required" title="required">*</abbr>Required End date
                </label>
                <div class="slds-form-element__icon help-text-icon">
                  <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
                    <i class="fa fa-info-circle" aria-hidden="true"></i>
                    <span class="slds-assistive-text">Help</span>
                  </button>
                  <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-display" role="tooltip" id="help" style="position:absolute;top:-50px;left:-15px;width: 175px;">
                    <div class="slds-popover__body">
                      Not more than next MTA
                    </div>
                  </div>
                </div>
                <v-menu v-model="showPickerEndDate"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="emoc.End_date__c"
                      v-validate="'required|date_format:dd/MM/yyyy|after:' + checkDateAfter"
                      data-vv-as="End date"
                      data-vv-value-path="inputValue"
                      name="End_date__c"
                      :error="errors.has('End_date__c')"
                      @blur="dateEnd = parseDate(emoc.End_date__c)"
                      placeholder="dd/mm/yyyy"
                      v-bind="attrs"
                      v-on="on"
                      append-icon="mdi-calendar"
                      autocomplete="off"
                      hide-details
                      outlined
                      dense
                      disabled
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    no-title
                    v-model="dateEnd"
                    @change="showPickerEndDate = false"
                    :min="minCalendar"
                  ></v-date-picker>
                </v-menu>
                <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('End_date__c') }}</span></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
import moment from 'moment'

export default {
  name: 'change-type',
  props: [
    'emoc'
  ],
  data () {
    return {
      showPickerComplDate: false,
      showPickerStartDate: false,
      showPickerEndDate: false,
      dateCompletion: '',
      dateStart: '',
      dateEnd: '',
      nameRules: [
        v => !!v || 'Name is required'
      ],
      minCalendar: moment().format('YYYY-MM-DD')
    }
  },
  computed: {
    checkDateAfter () {
      return moment(this.emoc.Start_date__c, 'DD/MM/YYYY').add(-1, 'days').format('DD/MM/YYYY')
    }
  },
  watch: {
    dateCompletion (val) {
      this.emoc.Completion_date__c = this.formatDate(this.dateCompletion)
    },
    dateStart (val) {
      this.emoc.Start_date__c = this.formatDate(this.dateStart)
    },
    dateEnd (val) {
      this.emoc.End_date__c = this.formatDate(this.dateEnd)
    },
    'emoc.Permanent__c' (newValue) {
      if (newValue) {
        this.emoc.Temporary__c = false
      }
    },
    'emoc.Temporary__c' (newValue) {
      if (newValue) {
        this.emoc.Permanent__c = false
      }
    }
  },
  methods: {
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    }
  }
}
</script>

<style scoped>
  .custom-icon-help-text {
    top: 0px;
  }
</style>


