<template>
  <div name="ta-checklist-popup">
    <header class="slds-modal__header">
      <div style="text-align: left;">
        <span class="slds-card__header-link slds-text-heading_small">{{displayChecklist.view ? 'View TA checklist' : 'TA checklist'}}</span>
        <button id="btnCloseTa" style="float: right; cursor: pointer;" title="Close" @click="onClose">
          <span class="slds-assistive-text">Close</span>
          <i class="fa fa-times" aria-hidden="true"></i>
        </button>
      </div>
    </header>
    <expandable-section-std v-for="(checklist, idx1) in taRemap" :title="checklist.Type__c" :name="'taChecklistExpand' + idx1 + 'Name'" :closedOnStart="idx1 === 0 ? false : true" :ref="'taChecklistExpand' + idx1" :key="'taChecklistExpand' + idx1">
      <div class="data-frame">
        <div class="scrollbar-table">
          <table id="ta-checklist-popup">
            <tr>
              <th width="5%">No.</th>
              <th width="35%">{{checklist.Type__c}}</th>
              <th width="5%">Yes</th>
              <th width="5%">N.A</th>
              <th width="30%">Remark</th>
              <th width="10%">Revision</th>
              <th width="10%">Last modified</th>
            </tr>
            <tr v-for="(question, idx2) in checklist.Question__c" :key="idx2">
              <td style="text-align: center;">{{question.Question_No__c}}</td>
              <td><div class="question-list"><span :title="question.Question__c">{{question.Question__c}}</span></div></td>
              <td style="text-align: center;">
                <checkbox-std v-model="question.Yes__c" :disabled="displayChecklist.view"
                              @onChange="clearAnotherCheckbox(idx1, idx2, 'Yes__c', question)"
                              :name="idx1 + 'Yes__c' + idx2"
                              :error="checkError(question)"/>
              </td>
              <td style="text-align: center;">
                <checkbox-std v-model="question.N_A__c" :disabled="displayChecklist.view"
                              @onChange="clearAnotherCheckbox(idx1, idx2, 'N_A__c', question)"
                              :error="checkError(question)"/>
              </td>
              <td>
                <input-text-std style="min-width: 300px;" v-model="question.Remark__c" :disabled="displayChecklist.view"/>
              </td>
              <td><div class="column-revision">{{ displayChecklist.view ? checklist.Version__c : '' }}</div></td>
              <td><div class="column-last-modified">{{ displayChecklist.view ? checklist.CreatedBy.Name : '' }}</div></td>
            </tr>
          </table>
        </div>
      </div>
    </expandable-section-std>
    <footer class="slds-modal__footer">
      <div style="text-align: center;">
        <button class="slds-button slds-button_neutral" @click="onClose">{{ displayChecklist.view ? 'Back' : 'Cancel' }}</button>
        <button v-show="displayChecklist.create" class="slds-button slds-button_brand" @click="callSave">Save</button>
      </div>
    </footer>
    <modal-after-save id="afterSaveTaChecklist"
                      @close="closePopupSuccess"
                      ref="popupAfterSaveTaChecklist"
                      :fixStyle="'height:700px;'" />
                      
    <modal-waiting ref="popupLoader" :fixStyle="'height:700px;'" />
  </div>
</template>
<script>
export default {
  name: 'ta-checklist-popup',
  props: [
    'emoc', 'displayChecklist', 'idxPosition', 'positionSelected'
  ],
  data () {
    return {
      resultSuccess: {},
      taRemap: [],
      flgCallSave: false
    }
  },
  mount () {
    this.remapFieldTaChk()
  },
  computed: {
    taChecklist () { return this.$store.state.taChecklist },
    genDataSave () {
      let remap = []
      this.taRemap.map(dm => {
        dm.Question__c.map(dmq => {
          remap.push({
            E_MOC_Application__c: dm.E_MOC_Application__c,
            Document__c: dm.Document__c,
            Sequence__c: dm.Sequence__c,
            Type__c: dm.Type__c,
            Question_No__c: dmq.Question_No__c,
            Question__c: dmq.Question__c,
            Yes__c: dmq.Yes__c || false,
            No__c: false,
            N_A__c: dmq.N_A__c || false,
            Remark__c: dmq.Remark__c || '',
            Version__c: dm.Version__c,
            Position__c: this.positionSelected
          })
        })
      })
      return remap
    }
  },
  watch: {
    taChecklist () {
      this.remapFieldTaChk()
    }
  },
  methods: {
    remapFieldTaChk () {
      let docSeq = 99999
      let remap = []
      if (this.displayChecklist.create) {
        this.taChecklist.map(dm => {
          if (dm.Sequence__c !== docSeq) {
            docSeq = dm.Sequence__c
            remap[dm.Sequence__c - 1] = {
              E_MOC_Application__c: dm.E_MOC_Application__c,
              Document__c: dm.Document__c,
              Type__c: dm.Type__c,
              Sequence__c: dm.Sequence__c,
              Version__c: dm.Version__c + 1,
              CreatedBy: dm.CreatedBy,
              Question__c: [{
                Question_No__c: dm.Question_No__c,
                Question__c: dm.Question__c,
                Yes__c: dm.Yes__c,
                No__c: dm.No__c,
                N_A__c: dm.N_A__c,
                Remark__c: dm.Remark__c
              }]
            }
          } else {
            remap[dm.Sequence__c - 1]['Question__c'].push({
              Question_No__c: dm.Question_No__c,
              Question__c: dm.Question__c,
              Yes__c: dm.Yes__c,
              No__c: dm.No__c,
              N_A__c: dm.N_A__c,
              Remark__c: dm.Remark__c
            })
          }
        })
      } else {
        let noItem = 0
        let noType = 0
        this.taChecklist.map(dm => {
          if (dm.Yes__c) {
            if (dm.Sequence__c !== docSeq) {
              noItem = 0
              docSeq = dm.Sequence__c
              remap[(++noType) - 1] = {
                E_MOC_Application__c: dm.E_MOC_Application__c,
                Document__c: dm.Document__c,
                Type__c: dm.Type__c,
                Sequence__c: dm.Sequence__c,
                Version__c: dm.Version__c,
                CreatedBy: dm.CreatedBy,
                Question__c: [{
                  Question_No__c: ++noItem,
                  Question__c: dm.Question__c,
                  Yes__c: dm.Yes__c,
                  No__c: dm.No__c,
                  N_A__c: dm.N_A__c,
                  Remark__c: dm.Remark__c
                }]
              }
            } else {
              remap[(noType) - 1]['Question__c'].push({
                Question_No__c: ++noItem,
                Question__c: dm.Question__c,
                Yes__c: dm.Yes__c,
                No__c: dm.No__c,
                N_A__c: dm.N_A__c,
                Remark__c: dm.Remark__c
              })
            }
          }
        })
      }
      this.taRemap = remap
    },
    clearAnotherCheckbox (idx1, idx2, field, dataRow) {
      if (field === 'Yes__c' && dataRow[field]) {
        dataRow.N_A__c = false
      }
      if (field === 'N_A__c' && dataRow[field]) {
        dataRow.Yes__c = false
      }
    },
    checkError (dataRow) {
      if (this.flgCallSave) {
        if (dataRow.Yes__c || dataRow.N_A__c) {
          return false
        } else {
          return true
        }
      } else {
        return false
      }
    },
    showPopupAfterSave () {
      this.$refs.popupAfterSaveTaChecklist.onOpen()
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    onClose () {
      this.displayChecklist.create = false
      this.displayChecklist.view = false
      let elements = document.getElementById('btnEditTa' + this.idxPosition)
      if (this.positionSelected === 'allPosition') {
        elements = document.getElementById('btnTaAllPosition')
      }
      this.$nextTick(() => {
        setTimeout(() => elements.focus(), 100)
      })
    },
    callSave () {
      this.flgCallSave = true
      let taRemap = this.taRemap
      let isError = false
      if (taRemap.length > 0) {
        this.$nextTick(() => {
          let elements = document.getElementsByName(0 + 'Yes__c' + 0)
          this.$nextTick(() => {
            setTimeout(() => elements[0].focus(), 100)
          })
        })
      }
      for (var idx1 = 0; idx1 < taRemap.length; idx1++) {
        for (var idx2 = 0; idx2 < taRemap[idx1].Question__c.length; idx2++) {
          if (!taRemap[idx1].Question__c[idx2].Yes__c && !taRemap[idx1].Question__c[idx2].N_A__c) {
            isError = true
            if (!this.$refs['taChecklistExpand' + idx1][0].isOpen) {
              this.$refs['taChecklistExpand' + idx1][0].toggle()
            }
            this.$nextTick(() => {
              let elements = document.getElementsByName(idx1 + 'Yes__c' + idx2)
              this.$nextTick(() => {
                setTimeout(() => elements[0].focus(), 100)
              })
            })
            break
          }
        }
        if (isError) {
          break
        }
      }
      if (!isError) {
        this.showPopupLoader()
        this.$nextTick(() => {
          let elements = document.getElementById('loadingPageId')
          setTimeout(() => elements.focus(), 100)
        })
        this.$store.dispatch('saveTaChecklist', JSON.stringify(this.genDataSave))
          .then(result => {
            this.closePopupLoader()
            if (result.statusSuccess) {
              this.$nextTick(() => {
                this.resultSuccess = result
              })
              this.showPopupAfterSave()
              this.$nextTick(() => {
                let elements = document.getElementById('btnafterSaveTaChecklist')
                setTimeout(() => elements.focus(), 100)
              })
            } else {
              console.log('Error', result)
            }
          })
      }
    }
  }
}
</script>

<style scoped>
  #ta-checklist-popup td, #ta-checklist-popup th {
    border: 1px solid #ddd;
    padding: 5px;
  }
  #ta-checklist-popup th {
    background-color: #fafaf9;
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: center;
  }
  #ta-checklist-popup td>.type-list {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    min-width: 150px;
    max-width: 190px;
  }
  #ta-checklist-popup td>.question-list {
    overflow: hidden;
    text-overflow: ellipsis;
    /* white-space: nowrap; */
    min-width: 400px;
    max-width: 400px;
  }
  #ta-checklist-popup tr:hover {background-color: #f2f2f2;}
  #ta-checklist-popup .slds-form-element__label {display: none;}
  #ta-checklist-popup .slds-checkbox_faux {margin-right: 0px;}
  .scrollbar-table {
    overflow: auto;
  }
  .column-revision { min-width: 50px; text-align: center; }
  .column-last-modified { min-width: 200px; text-align: center; }
</style>


