      }
      if (this.showTechnicalReview) {
        this.$nextTick(() => {
          if (this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.isOpen) {
            this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.toggle()
          }
        })
      }
      if (this.showTechnicalReview && this.emoc.Show_Technical_review_by_MOC__c) {
        this.$nextTick(() => {
          if (this.$refs.technicalReviewMocFocalCmp.$refs.expandTaReviewMocFocal.isOpen) {
            this.$refs.technicalReviewMocFocalCmp.$refs.expandTaReviewMocFocal.toggle()
          }
        })
      }
      if (this.showPssrCategory) {
        this.$nextTick(() => {
          if (this.$refs.pssrCategoryCmp.$refs.expandPssrCategory.isOpen) {
            this.$refs.pssrCategoryCmp.$refs.expandPssrCategory.toggle()
          }
        })
      }
      if (this.showMocAccomplishment) {
        this.$nextTick(() => {
          if (this.$refs.mocAccomplishmentCmp.$refs.expandMocAccomplishment.isOpen) {
            this.$refs.mocAccomplishmentCmp.$refs.expandMocAccomplishment.toggle()
          }
        })
      }
    }
  },
  components: {
    emocTitle,
    budgetType,
    functionalLocation,
    functionalLocationAssociate,
    categoryType,
    mocType,
    changeType,
    inputChangeDetail,
    ramRating,
    docReqChecklist,
    mocFocalPoint,
    hazardChecklist,
    riskAnalysis,
    ta2,
    technicalReview,
    taReviewMocFocal,
    notification,
    pssrCategory,
    mocAccomplishment,
    lessonLearned
  }
}
</script>


