<template>
  <div name="category-type">
    <expandable-section-std v-show="emoc.Mechanical_change__c" title="Mechanical change" :closedOnStart="true">
      <div class="data-frame">
        <div class="row">
          <template v-for="mechanical in keyOfMechanical">
            <div v-show="emoc[mechanical.key]" class="col-md-4">
              <checkbox-std
                v-model="emoc[mechanical.key]"
                :label="mechanical.desc"
                :inline="true"
                disabled
              />
            </div>
          </template>
        </div>
      </div>
    </expandable-section-std>
    <expandable-section-std v-show="emoc.Instrument_change__c" title="Instrument change" :closedOnStart="true">
      <div class="data-frame">
        <div class="row">
          <template v-for="instrument in keyOfInstrument">
            <div v-show="emoc[instrument.key]" class="col-md-4">
              <checkbox-std
                v-model="emoc[instrument.key]"
                :label="instrument.desc"
                :inline="true"
                disabled
              />
            </div>
          </template>
        </div>
      </div>
    </expandable-section-std>
    <expandable-section-std v-show="emoc.Electrical_change__c" title="Electrical change" :closedOnStart="true">
      <div class="data-frame">
        <div class="row">
          <template v-for="electrical in keyOfElectrical">
            <div v-show="emoc[electrical.key]" class="col-md-4">
              <checkbox-std
                v-model="emoc[electrical.key]"
                :label="electrical.desc"
                :inline="true"
                disabled
              />
            </div>
          </template>
        </div>
      </div>
    </expandable-section-std>
    <expandable-section-std v-show="emoc.Civil_change__c" title="Civil change" :closedOnStart="true">
      <div class="data-frame">
        <div class="row">
          <template v-for="electrical in keyOfCivil">
            <div v-show="emoc[electrical.key]" class="col-md-4">
              <checkbox-std
                v-model="emoc[electrical.key]"
                :label="electrical.desc"
                :inline="true"
                disabled
              />
            </div>
          </template>
          <!-- <div v-show="emoc.Buildling_and_Road__c" class="col-md-4">
            <checkbox-std
              v-model="emoc.Buildling_and_Road__c"
              label="Building and Roa"
              :inline="true"
              disabled
            />
          </div>
          <div v-show="emoc.Structures_foundations__c" class="col-md-4">
            <checkbox-std
                  v-model="emoc.Structures_foundations__c"
                  label="Structures / foundations"
                  :inline="true"
                  disabled
                />
          </div>
          <div v-show="emoc.Drainage_system_AOC_COC_etc_Bund__c" class="col-md-4">
            <checkbox-std
              v-model="emoc.Drainage_system_AOC_COC_etc_Bund__c"
              label="Drainage system (AOC, COC, etc) / Bund"
              :inline="true"
              disabled
            />
          </div> -->
        </div>
      </div>
    </expandable-section-std>
    <expandable-section-std v-show="emoc.Catalyst_Chemical__c" title="Catalyst & Chemical" :closedOnStart="true">
      <div class="data-frame">
        <div class="row">
          <div class="col-9">
            <checkbox-std
              v-model="emoc.Catalyst_Chemical__c"
              label="Catalyst & Chemical"
              :inline="true"
              disabled
            />
          </div>
        </div>
      </div>
    </expandable-section-std>
    <expandable-section-std v-show="emoc.Design_limit_IOW_change__c" title="Design limit + Integrity Operating window (IOW)" :closedOnStart="true">
      <div class="data-frame">
        <div class="row">
          <div class="col-9">
            <checkbox-std
              v-model="emoc.Design_limit_IOW_change__c"
              label="Design limit + Integrity Operating window (IOW)"
              :inline="true"
              disabled
            />
          </div>
        </div>
      </div>
    </expandable-section-std>
    <expandable-section-std v-show="emoc.Tank_change_service__c" title="Tank change service" :closedOnStart="true">
      <div class="data-frame">
        <div class="row">
          <div class="col-9">
            <checkbox-std
              v-model="emoc.Tank_change_service__c"
              label="Tank change service"
              :inline="true"
              disabled
            />
          </div>
        </div>
      </div>
    </expandable-section-std>
    <expandable-section-std v-show="emoc.Idled_Retired_Assets_equipment__c" title="Idled / Retired Assets (equipment)" :closedOnStart="true">
      <div class="data-frame">
        <div class="row">
          <div v-show="emoc.Demolition__c" class="col-md-4">
            <checkbox-std
              v-model="emoc.Demolition__c"
              label="Demolition"
              :inline="true"
              disabled
            />
          </div>
          <div v-show="emoc.Mothballing__c" class="col-md-4">
            <checkbox-std
              v-model="emoc.Mothballing__c"
              label="Mothballing/Preservation"
              :inline="true"
              disabled
            />
          </div>
          <div v-show="emoc.Preservation_unit__c" class="col-md-4">
            <checkbox-std
              v-model="emoc.Preservation_unit__c"
              label="Retiring"
              :inline="true"
              disabled
            />
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'category-type',
  props: [
    'emoc'
  ],
  data () {
    return {
      keyOfMechanical: [
        {key: 'Air_Cooled_Exchangers__c', desc: 'Air Cooled Exchangers'},
        {key: 'Fired_Heaters_Fire_Boiler__c', desc: 'Fired Heaters / Fire Boiler'},
        {key: 'Pipes_Flanges_Valve__c', desc: 'Pipes / Flanges / Valve'},
        {key: 'Pressure_Vessels__c', desc: 'Pressure Vessels/Columns'},
        {key: 'Tracing_steam_Steam_trap__c', desc: 'Tracing steam / Steam trap'},
        {key: 'Rotating_equipment__c', desc: 'Rotating equipment'},
        {key: 'Relief_Systems__c', desc: 'Relief Systems'},
        {key: 'Shell_Tube_Heat_Exchangers__c', desc: 'Shell & Tube Heat Exchangers'},
        {key: 'Tanks__c', desc: 'Tanks'},
        {key: 'SBM_CBM__c', desc: 'SBM / CBM'},
        {key: 'HVAC_Refrigeration__c', desc: 'HVAC & Refrigeration'},
        {key: 'Loading_arm_Jetty_Lorry__c', desc: 'Loading arm (Jetty & Lorry)'},
        {key: 'Hydraulic_power_unit__c', desc: 'Hydraulic power unit'},
        {key: 'Safety_Equipment__c', desc: 'Safety Equipment'},
        // {key: 'Golden_weld__c', desc: 'Golden weld'},
        // {key: 'Hot_tap__c', desc: 'Hot tap'},
        {key: 'Temporary_repair__c', desc: 'Temporary repair'}
      ],
      keyOfInstrument: [
        {key: 'Alarm__c', desc: 'Alarm'},
        {key: 'APC__c', desc: 'APC'},
        {key: 'SIF__c', desc: 'SIF'},
        {key: 'Range__c', desc: 'Range'},
        {key: 'Control_valve__c', desc: 'Control valve'},
        {key: 'Instrumentation_equipment__c', desc: 'Instrumentation equipment'},
        {key: 'Base_layer_control_DCS_PLC_TAS_TAM__c', desc: 'Base layer control (DCS), PLC, TAS/TAM'},
        {key: 'QMI__c', desc: 'QMI'},
        {key: 'Valve__c', desc: 'Valve'},
        {key: 'Fire_Gas_detection_system__c', desc: 'Fire & Gas detection system'}
      ],
      keyOfElectrical: [
        { key: 'Electrical_equipment_system__c', desc: 'Electrical equipment / system' },
        { key: 'Emergency_Backup_Systems__c', desc: 'Emergency Backup Systems' },
        { key: 'Communication__c', desc: 'Communication' },
        { key: 'Driver_Actuator_process_at_Valve__c', desc: 'Driver/Actuator (process) at Valve' }
      ],
      keyOfCivil: [
        { key: 'Buildling_and_Road__c', desc: 'Building and Road' },
        { key: 'Structures_foundations__c', desc: 'Structures / foundations' },
        { key: 'Drainage_system_AOC_COC_etc_Bund__c', desc: 'Drainage system (AOC, COC, etc) / Bund' },
        { key: 'Refractory_Fireproof__c', desc: 'Refractory/Fireproof' },
        { key: 'Insulation__c', desc: 'Insulation' },
        { key: 'Painting_and_coating__c', desc: 'Painting and coating' }
      ]
    }
  },
  computed: {
    checkMechanicalChange () {
      return this.emoc.Mechanical_change__c
    },
    checkInstrumentChange () {
      return this.emoc.Instrument_change__c
    },
    checkElectricalChange () {
      return this.emoc.Electrical_change__c
    },
    checkCivilChange () {
      return this.emoc.Civil_change__c
    },
    checkCatalystChange () {
      return this.emoc.Catalyst_Chemical__c
    },
    checkDesignLimitChange () {
      return this.emoc.Design_limit_IOW_change__c
    },
    checkTankChange () {
      return this.emoc.Tank_change_service__c
    },
    checkIdledRetiredChange () {
      return this.emoc.Idled_Retired_Assets_equipment__c
    },
    showLabelSubCate () {
      return this.checkMechanicalChange || this.checkInstrumentChange || this.checkElectricalChange || this.checkCivilChange || this.checkCatalystChange || this.checkDesignLimitChange || this.checkTankChange || this.checkIdledRetiredChange
    }
  }
}
</script>


