<template>
  <div class="create-emoc">
      <common-workflow v-show="displayMainPage"
                       :emoc="emoc"
                       :displayDocReq="displayDocReq"
                       :displayHazard="displayHazard"
                       :displayTa="displayTa"
                       :displayQaQc="displayQaQc"
                       :displayConsolidate="displayConsolidate"
                       :setTaPositionSelected="setTaPositionSelected"
                       :setCallFrom="setCallFrom"
                       ref="commonWorkflow" />
      <doc-req-checklist-popup v-if="displayDocReq.create || displayDocReq.view"
                              :emoc="emoc"
                              :displayChecklist="displayDocReq"
                              :callDocReqFrom="callDocReqFrom"
                              ref="refDocReqChecklist"/>
      <hazard-checklist-popup v-if="displayHazard.create || displayHazard.view"
                              :emoc="emoc"
                              :displayChecklist="displayHazard"
                              ref="refHazardChecklist"/>
      <ta-checklist-popup v-if="displayTa.create || displayTa.view"
                          :emoc="emoc"
                          :displayChecklist="displayTa"
                          :idxPosition="idxPosition"
                          :positionSelected="positionSelected"
                          ref="refTaChecklist"/>
      <qa-qc-checklist-popup v-if="displayQaQc.create || displayQaQc.view"
                            :emoc="emoc"
                            :displayChecklist="displayQaQc"
                            ref="refQaQcChecklist"/>
      <consolidate-comment-popup v-if="displayConsolidate.create || displayConsolidate.view"
                                :emoc="emoc"
                                :displayChecklist="displayConsolidate"
                                ref="refConsolidateComment"/>
  </div>
</template>
<script>
import commonWorkflow from './CommonWorkflow.vue'
import functionalOverride from './FunctionalOverride.vue'
import docReqChecklistPopup from './DocReqChecklistPopup.vue'
import hazardChecklistPopup from './HazardChecklistPopup.vue'
import taChecklistPopup from './TaChecklistPopup.vue'
import qaQcChecklistPopup from './QaQcChecklistPopup.vue'
import consolidateCommentPopup from './ConsolidateCommentPopup.vue'

export default {
  name: 'create-emoc',
  data () {
    return {
      numpage: 1,
      hasError: false,
      errorBagItems: [],
      resultSuccess: {},
      recId: '',
      displayDocReq: {
        create: false,
        view: false
      },
      displayHazard: {
        create: false,
        view: false
      },
      displayTa: {
        create: false,
        view: false
      },
      displayQaQc: {
        create: false,
        view: false
      },
      displayConsolidate: {
        create: false,
        view: false
      },
      idxPosition: '',
      positionSelected: '',
      callDocReqFrom: ''
    }
  },
  created () {
    this.$store.dispatch('receiveLookupFncLocation', { type: 'main', sendParam: '' })
    this.$store.dispatch('receiveLookupUser', { type: 'main', sendParam: '' })
  },
  mounted () {
    const queryString = window.location.search
    const urlParams = new URLSearchParams(queryString)
    this.recId = urlParams.get('id')
    let paramSearch = {
      emocApplicationId: this.recId
    }
    if (this.emocEditEnable) {
      this.$store.dispatch('receiveEmocApplication', paramSearch)
    }
  },
  computed: {
    emoc () { return this.$store.state.emoc },
    enableBtnNext () {
      let conditionPage = false
      if ((this.emoc.CPM_CAPEX__c || this.emoc.OPM_OPEX__c) && this.numpage === 1) {
        conditionPage = true
      } else if ((this.emoc.Common_Workflow__c || this.emoc.Functional_Override__c) && this.numpage === 2) {
        conditionPage = true
      } else {
        conditionPage = false
      }
      return conditionPage
    },
    emocEditEnable () {
      return this.recId && this.recId !== ''
    },
    displayMainPage () {
      return !this.displayDocReq.create && !this.displayHazard.create && !this.displayTa.create && !this.displayQaQc.create && !this.displayConsolidate.create && !this.displayDocReq.view && !this.displayHazard.view && !this.displayTa.view && !this.displayQaQc.view && !this.displayConsolidate.view
    }
  },
  watch: {
    'emoc.CPM_CAPEX__c' (newValue) {
      if (newValue) {
        this.emoc.OPM_OPEX__c = false
      }
    },
    'emoc.OPM_OPEX__c' (newValue) {
      if (newValue) {
        this.emoc.CPM_CAPEX__c = false
      }
    },
    'emoc.Common_Workflow__c' (newValue) {
      if (newValue) {
        this.emoc.Functional_Override__c = false
      }
    },
    'emoc.Functional_Override__c' (newValue) {
      if (newValue) {
        this.emoc.Common_Workflow__c = false
      }
    }
  },
  methods: {
    showPopup () {
      this.$refs.popupAfterSave.onOpen()
    },
    closePopup () {
      this.$refs.popupAfterSave.onClose()
    },
    clickNext () {
      if (this.emoc.OPM_OPEX__c && this.numpage === 1) {
        this.numpage++
      } else if ((this.emoc.Common_Workflow__c || this.emoc.Functional_Override__c) && this.numpage === 2) {
        this.numpage++
      } else {
        console.log('Redirect to main')
      }
    },
    clickPrevious () {
      this.numpage--
      this.hasError = false
    },
    clickCancel () {
      this.numpage = 1
    },
    getLabelFields (field) {
      let listFieldsLabel = {
        Functional_Location__c: 'Functional Location',
        Completion_date__c: 'Required Completion date',
        Start_date__c: 'Required Start date',
        End_date__c: 'Required End date',
        Description_of_change__c: 'Description of change',
        Reason_of_change__c: 'Reason of change',
        Justification__c: 'Justification'
      }
      return listFieldsLabel[field]
    },
    setTaPositionSelected (idx, position) {
      this.idxPosition = idx
      this.positionSelected = position
    },
    setCallFrom (callForm) {
      this.callDocReqFrom = callForm
    }
  },
  components: {
    commonWorkflow,
    functionalOverride,
    docReqChecklistPopup,
    hazardChecklistPopup,
    taChecklistPopup,
    qaQcChecklistPopup,
    consolidateCommentPopup
  }
}
</script>

<style src="../assets/style.css"></style>


