<template>
  <div name="technical-review">
    <expandable-section-std id="expandTechnicalReview" title="Technical Review" :closedOnStart="false" ref="expandTechnicalReview">
      <expandable-section-std v-for="(dataManMap, idx) in techTaMan" :id="'expandTechnicalReview' + idx" :title="'Technical Review #' + (idx + 1)" :closedOnStart="!dataManMap.Enabled_TA_Checklist" :ref="'expandTechnicalReview' + idx" :key="'expandTechnicalReview' + idx">
        <div class="data-frame">
          <div class="row">
            <div class="col-sm-12" style="padding-bottom: 0px;">
              <div class="slds-border_bottom topic2">
                <label>TA2</label>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-sm-6">
              <input-text-std v-model="dataManMap.TA2_Indicator__c" label="Indicator / Name" class="fontInput" disabled></input-text-std>
            </div>
            <div class="col-sm-6">
              <input-text-std v-model="dataManMap.TA2_Expert_Field__c" label="Expert field" class="fontInput" disabled></input-text-std>
            </div>
          </div>
          <div class="row">
            <div class="col-12" style="padding-bottom: 0px;">
              <div class="slds-border_bottom topic2">
                <label>TA3</label>
              </div>
            </div>
            <div class="col-sm-6" style="padding-top: 0px;">
              <lookups-std v-model="dataManMap.TA3_Indicator__c"
                           data-vv-as="Indicator / Name"
                           data-vv-value-path="inputValue"
                           label="Indicator / Name"
                           placeholder="Search..."
                           :options="optionsTa3ManMaster(dataManMap.Position__c)"
                           :error="errors.has('TA3_Indicator__c' + idx)"
                           :name="'TA3_Indicator__c' + idx"
                           @search="filterTa3ManMaster(arguments[0], dataManMap.Position__c)"
                           @setDataRow="setDataRow(arguments[0], dataManMap)"
                           icon="people" disabled
                           loading
                           :classCustom="'font-size: 11px;'"
                           >
              </lookups-std>
            </div>
            <div class="col-sm-6" style="padding-top: 0px;">
              <input-text-std v-model="dataManMap.TA3_Expert_Field__c" label="Expert field" class="fontInput" disabled></input-text-std>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <textarea-std
                v-model="dataManMap.TA3_Comment__c"
                name="TA3_Comment__c"
                label="TA2 / TA3 Comment"
                :rows="1"
                disabled
                class="fontInput"
              >
              </textarea-std>
            </div>
          </div>
          <div v-show="!statusRejectCancel" class="row">
            <div class="col-sm-12">
              <button v-if="!(statusClosed || statusLessonLearn)" :id="'btnEditTa' + idx" class="slds-button slds-button_neutral" @click="callTaChecklist(idx, dataManMap.Position__c)" :disabled="!dataManMap.Enabled_TA_Checklist">Edit TA Checklist</button>
              <button v-show="dataManMap.Ta_Checklist_Result > 0" class="slds-button slds-button_neutral" @click="callViewTaChecklist(idx, dataManMap.Position__c)">View TA Checklist</button>
            </div>
          </div>
          <div class="row">
            <div class="col-sm-6">
              <checkbox-std
                  v-model="dataManMap.Compliance_Standard__c"
                  label="Compliance Standard"
                  :inline="true" disabled
                  @onChange="clearAnotherCheckbox('Compliance_Standard__c', dataManMap)"
              />
            </div>
            <div v-show="dataManMap.Compliance_Standard__c" class="col-sm-6">
              <textarea-std
                v-model="dataManMap.Compliance_Standard_Remark__c"
                label="Remark" 
                disabled
                :rows="1"
                class="fontInput"
              >
              </textarea-std>
            </div>
          </div>
          <div class="row">
            <div class="col-sm-6">
              <checkbox-std
                  v-model="dataManMap.Deviate_Standard__c"
                  label="Deviate Standard"
                  :inline="true" disabled
                  @onChange="clearAnotherCheckbox('Deviate_Standard__c', dataManMap)"
              />
            </div>
            <div v-show="dataManMap.Deviate_Standard__c" class="col-sm-6">
              <button v-show="emoc.Deviation_Form__c !== ''" class="slds-button slds-button_neutral" @click="$refs.linkDeviateRef[idx].click()">Deviation Form</button>
              <!-- <label class="slds-form-element__label">Deviation Form</label><br> -->
              <a hidden ref="linkDeviateRef" :href="emoc.Deviation_Form__c" target="_blank">{{emoc.Deviation_Form__c}}</a>
            </div>
          </div>

          <div class="row" v-if="dataManMap.Deviate_Standard__c">
            <div class="col-sm-6 padding-l40">
              <checkbox-std
                  v-model="dataManMap.I_have_uploaded_the_deviation_form__c"
                  label="I have uploaded the deviation form with authorized person approval."
                  :inline="true"
                  :name="'I_have_uploaded_the_deviation_form__c' + idx"
                  @onChange="setAction4(idx, dataManMap)"
                  disabled
              />
            </div>
            <div class="col-sm-6">
              <lookups-std v-model="dataManMap.Action_by_deviated_form__c"
                           data-vv-as="Action by"
                           data-vv-value-path="inputValue"
                           label="Action by"
                           placeholder="Search..."
                           :options="optionsLookupUser4(idx, dataManMap)"
                           :error="errors.has('Action_by_deviated_form__c' + idx)"
                           :name="'Action_by_deviated_form__c' + idx"
                           @search="filterLookupUser4('lookupUser4' + idx, arguments[0])"
                           @setDataRow="setDataRow4(idx, arguments[0])"
                           icon="people"
                           loading
                           disabled 
                           :classCustom="'font-size: 11px;'">  
    
              </lookups-std>
            </div>
          </div>

          <div class="row">
            <div class="col-sm-6">
              <checkbox-std
                  v-model="dataManMap.N_A__c"
                  label="N/A"
                  :inline="true" disabled
                  @onChange="clearAnotherCheckbox('N_A__c', dataManMap)"
              />
            </div>
            <div v-show="dataManMap.N_A__c" class="col-sm-6">
              <textarea-std
                v-model="dataManMap.N_A_Remark__c"
                label="Remark" 
                disabled
                :rows="1"
                class="fontInput"
              >
              </textarea-std>
            </div>
          </div>
          <!-- <div v-show="dataManMap.flgShowIHaveFinishTechnicalReview" class="row">
            <div class="col-sm-6">
              <checkbox-std
                  v-model="dataManMap.I_have_finished_technical_review__c"
                  label="I have finished technical review"
                  :inline="true" disabled
              />
            </div>
          </div> -->
          <div class="slds-border_bottom topic2"></div>
          <div class="row" v-if="dataManMap.flgShowTa3Review">
            <div class="col-sm-6">
              <checkbox-std
                  v-model="dataManMap.TA3_s_review_has_been_completed__c"
                  label="TA3’s review has been completed"
                  :inline="true"
                  :name="'TA3_s_review_has_been_completed__c' + idx"
                  @onChange="setAction3(idx, dataManMap)"
                  disabled
              />
            </div>
            <div class="col-sm-6" v-if="dataManMap.TA3_s_review_has_been_completed__c">
              <lookups-std v-model="dataManMap.Action_by_TA3__c"
                           data-vv-as="Action by"
                           data-vv-value-path="inputValue"
                           label="Action by"
                           placeholder="Search..."
                           :options="optionsLookupUser3(idx, dataManMap)"
                           :error="errors.has('Action_by_TA3__c' + idx)"
                           :name="'Action_by_TA3__c' + idx"
                           @search="filterLookupUser3('lookupUser3' + idx, arguments[0])"
                           @setDataRow="setDataRow3(idx, arguments[0])"
                           icon="people"
                           loading
                           disabled 
                           :classCustom="'font-size: 11px;'">        
              </lookups-std>
            </div>
          </div>
          <div class="row" v-if="(dataManMap.Compliance_Standard__c || dataManMap.Deviate_Standard__c || dataManMap.N_A__c) && (dataManMap.Ta_Checklist_Result > 0)">
            <div class="col-sm-6">
              <checkbox-std
                  v-model="dataManMap.The_comment_need_to_be_verified__c"
                  label="Require Discipline TA review again after MOC focal point incorporated TA's comment."
                  :inline="true"
                  :name="'The_comment_need_to_be_verified__c' + idx"
                  @onChange="setAction2(idx, dataManMap, 'The_comment_need_to_be_verified__c')"
                  disabled
              />
            </div>
            <div class="col-sm-6" v-if="dataManMap.The_comment_need_to_be_verified__c">
              <lookups-std v-model="dataManMap.Action_by_TA2__c"
                           data-vv-as="Action by"
                           data-vv-value-path="inputValue"
                           label="Action by"
                           placeholder="Search..."
                           :options="optionsLookupUser2(idx, dataManMap)"
                           :error="errors.has('Action_by_TA2__c' + idx)"
                           :name="'Action_by_TA2__c' + idx"
                           @search="filterLookupUser2('lookupUser2' + idx, arguments[0])"
                           @setDataRow="setDataRow2(idx, arguments[0])"
                           icon="people"
                           loading
                           disabled
                           :classCustom="'font-size: 11px;'" >
    
              </lookups-std>
            </div>
          </div>

          <div class="row" v-if="(dataManMap.Compliance_Standard__c || dataManMap.Deviate_Standard__c || dataManMap.N_A__c) && (dataManMap.Ta_Checklist_Result > 0)">
            <div class="col-sm-6">
              <checkbox-std
                  v-model="dataManMap.TA2_s_review_has_been_completed__c"
                  label="TA2's review is completed (including monitoring program if applicable)."
                  :inline="true"
                  :name="'TA2_s_review_has_been_completed__c' + idx"
                  @onChange="setAction2(idx, dataManMap, 'TA2_s_review_has_been_completed__c')"
                  disabled
              />
            </div>
            <div class="col-sm-6" v-if="dataManMap.TA2_s_review_has_been_completed__c">
              <lookups-std v-model="dataManMap.Action_by_TA2__c"
                           data-vv-as="Action by"
                           data-vv-value-path="inputValue"
                           label="Action by"
                           placeholder="Search..."
                           :options="optionsLookupUser2(idx, dataManMap)"
                           :error="errors.has('Action_by_TA2__c' + idx)"
                           :name="'Action_by_TA2__c' + idx"
                           @search="filterLookupUser2('lookupUser2' + idx, arguments[0])"
                           @setDataRow="setDataRow2(idx, arguments[0])"
                           icon="people"
                           loading
                           disabled
                           :classCustom="'font-size: 11px;'">    
              </lookups-std>
            </div>
          </div>

        </div>
      </expandable-section-std>
    </expandable-section-std>
  </div>
</template>
<script>
import taChecklistPopup from './TaChecklistPopup.vue'

export default {
  name: 'technical-review',
  props: ['emoc', 'statusRejectCancel', 'displayChecklist', 'setTaPositionSelected', 'statusClosed', 'statusLessonLearn'],
  data () {
    return {
      resultSuccess: {},
      lookupUserType: 'lookupUser2',
      lookupUserType3: 'lookupUser3',
      lookupUserType4: 'lookupUser4'
    }
  },
  computed: {
    techTaMan () { return this.$store.state.taManMapping },
    lookupTa3 () { return this.$store.state.lookupTa3 },
    ta3ManMaster () { return this.$store.state.ta3ManMaster },
    lookupUser () { return this.$store.state.lookupUser }
  },
  methods: {
    filterTa3ManMaster (searchParam, position) {
      this.$store.dispatch('receiveTa3ManMaster', { sendParam: searchParam, position: position })
    },
    filterLookupUser2 (type, searchParam) {
      this.$store.dispatch('receiveLookupUser', {type, sendParam: searchParam})
    },
    filterLookupUser3 (type, searchParam) {
      this.$store.dispatch('receiveLookupUser', {type, sendParam: searchParam})
    },
    filterLookupUser4 (type, searchParam) {
      this.$store.dispatch('receiveLookupUser', {type, sendParam: searchParam})
    },
    optionsTa3ManMaster (position) {
      return this.ta3ManMaster[position].map(dm => {
        return {
          value: dm.Id,
          label: dm.Indicator__c,
          Expert_field_for__c: dm.Expert_field_for__c,
          Indicator__c: dm.Indicator__c,
          icon: 'people'
        }
      })
    },
    setDataRow (lookupRow, dataRow) {
      dataRow.TA3_Expert_Field__c = lookupRow.Expert_field_for__c
      dataRow.TA3_Position_c__c = lookupRow.TA3_Indicator__c
      if (typeof dataRow.TA3_Indicator__c === 'undefined' || dataRow.TA3_Indicator__c === '') {
        this.$store.dispatch('receiveTa3ManMaster', { sendParam: '', position: dataRow.Position__c })
      }
    },
    optionsLookupUser2 (rowIndex, dataRow) {
      if (typeof this.lookupUser[this.lookupUserType + rowIndex] !== 'undefined') {
        return this.lookupUser[this.lookupUserType + rowIndex].map(dm => {
          dm['value'] = dm.Id
          dm['label'] = dm.FirstName + ' ' + dm.LastName
          dm['icon'] = 'people'
          return dm
        })
      } else {
        this.filterLookupUser2('lookupUser2' + rowIndex, dataRow.Action_by_TA2__c)
        return []
      }
    },
    optionsLookupUser3 (rowIndex, dataRow) {
      if (typeof this.lookupUser[this.lookupUserType3 + rowIndex] !== 'undefined') {
        return this.lookupUser[this.lookupUserType3 + rowIndex].map(dm => {
          dm['value'] = dm.Id
          dm['label'] = dm.FirstName + ' ' + dm.LastName
          dm['icon'] = 'people'
          return dm
        })
      } else {
        this.filterLookupUser3('lookupUser3' + rowIndex, dataRow.Action_by_TA3__c)
        return []
      }
    },
    optionsLookupUser4 (rowIndex, dataRow) {
      if (typeof this.lookupUser[this.lookupUserType4 + rowIndex] !== 'undefined') {
        return this.lookupUser[this.lookupUserType4 + rowIndex].map(dm => {
          dm['value'] = dm.Id
          dm['label'] = dm.FirstName + ' ' + dm.LastName
          dm['icon'] = 'people'
          return dm
        })
      } else {
        this.filterLookupUser4('lookupUser4' + rowIndex, dataRow.Action_by_deviated_form__c)
        return []
      }
    },
    setDataRow2 (rowIndex, dataRow) {
      if (typeof dataRow.Action_by_TA2__c === 'undefined') {
        this.filterLookupUser2('lookupUser2' + rowIndex, '')
      }
    },
    setDataRow3 (rowIndex, dataRow) {
      if (typeof dataRow.Action_by_TA3__c === 'undefined') {
        this.filterLookupUser3('lookupUser3' + rowIndex, '')
      }
    },
    setDataRow4 (rowIndex, dataRow) {
      if (typeof dataRow.Action_by_deviated_form__c === 'undefined') {
        this.filterLookupUser4('lookupUser4' + rowIndex, '')
      }
    },
    setAction2 (rowIndex, dataRow, field) {
      dataRow.Action_by_TA2__c = (dataRow.TA2_s_review_has_been_completed__c === true || dataRow.The_comment_need_to_be_verified__c === true) ? this.emoc.User_Login : ''
      if (typeof dataRow.Action_by_TA2__c !== 'undefined' && dataRow.Action_by_TA2__c !== '') {
        this.filterLookupUser2('lookupUser2' + rowIndex, this.emoc.User_Login)
      }
    },
    setAction3 (rowIndex, dataRow) {
      dataRow.Action_by_TA3__c = (dataRow.TA3_s_review_has_been_completed__c === true) ? this.emoc.User_Login : ''
      if (typeof dataRow.Action_by_TA3__c !== 'undefined' && dataRow.Action_by_TA3__c !== '') {
        this.filterLookupUser3('lookupUser3' + rowIndex, this.emoc.User_Login)
      }
    },
    setAction4 (rowIndex, dataRow) {
      dataRow.Action_by_deviated_form__c = (dataRow.I_have_uploaded_the_deviation_form__c === true) ? this.emoc.User_Login : ''
      if (typeof dataRow.Action_by_deviated_form__c !== 'undefined' && dataRow.Action_by_deviated_form__c !== '') {
        this.filterLookupUser4('lookupUser4' + rowIndex, this.emoc.User_Login)
      }
    },
    clearAnotherCheckbox (field, dataRow) {
      if (field === 'Compliance_Standard__c') {
        dataRow.Deviate_Standard__c = false
        dataRow.N_A__c = false
        dataRow.N_A_Remark__c = ''
      } else if (field === 'Deviate_Standard__c') {
        dataRow.Compliance_Standard__c = false
        dataRow.N_A__c = false
        dataRow.N_A_Remark__c = ''
        dataRow.Compliance_Standard_Remark__c = ''
        dataRow.I_have_uploaded_the_deviation_form__c = false
        dataRow.Action_by_deviated_form__c = ''
      } else if (field === 'N_A__c') {
        dataRow.Compliance_Standard__c = false
        dataRow.Deviate_Standard__c = false
        dataRow.Compliance_Standard_Remark__c = ''
      }
    },
    callTaChecklist (idx, position) {
      // this.positionSelected = position
      this.setTaPositionSelected(idx, position)
      this.displayChecklist.create = true
      // this.showPopupTaChecklist()
      this.$store.dispatch('receiveTaChecklist', {Id: this.emoc.Id, Position__c: position})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCloseTa')
        setTimeout(() => elements.focus(), 100)
      })
    },
    callViewTaChecklist (idx, position) {
      this.setTaPositionSelected(idx, position)
      this.displayChecklist.view = true
      // this.showPopupTaChecklist()
      this.$store.dispatch('receiveTaChecklist', {Id: this.emoc.Id, Position__c: position})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCloseTa')
        setTimeout(() => elements.focus(), 100)
      })
    }
  },
  components: {
    taChecklistPopup
  }
}
</script>

<style scoped>
  .fontInput {
    font-size: 11px;
  }
  .padding-l40 {
    padding-left: 40px;
  }
</style>


