<template>
  <div name="emoc-title">
    <expandable-section-std title="E-MOC Title" :closedOnStart="false" ref="expandEmocTitle">
      <div class="data-frame">
        <div class="row">

          <div class="col-12">
            <textarea-std
              v-model="emoc.E_MOC_Title__c"
              data-vv-as="E-MOC Title"
              name="E_MOC_Title__c"
              label="E-MOC Title"
              :required="true"
              disabled
              :rows="1"
            >
            </textarea-std>
          </div>

        </div>
      </div>
      
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'emoc-title',
  props: [
    'emoc'
  ]
}
</script>


