<template>
  <div name="moc-focal-point">
    <expandable-section-std title="MOC Focal Point" ref="expandMocFocalPoint" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-md-6">
            <lookups-std v-model="emoc.MOC_Focal_Point_Name__c"
                         label="MOC Focal Point Name"
                         placeholder="Search..."
                         :options="optionsLookupFocalPoint"
                         :required="true"
                         name="MOC_Focal_Point_Name__c"
                         @search="filterLookupFocalPoint(arguments[0])"
                         icon="people"
                         disabled
                         loading>
            </lookups-std>
          </div>
          <div class="col-md-6">
            <input-text-std v-model="emoc.MOC_Focal_Point_ID__c" label="MOC Focal Point ID" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <input-text-std v-model="emoc.Position__c" label="Position" disabled></input-text-std>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'moc-focal-point',
  props: [
    'emoc'
  ],
  data () {
    return {
      lookupType: 'moc_focal_point'
    }
  },
  created () {
    if (this.emoc.MOC_Focal_Point_Name__c !== '' && this.emoc.MOC_Focal_Point_Name__c) {
      this.filterLookupFocalPoint(this.emoc.MOC_Focal_Point_Name__c)
    } else {
      this.filterLookupFocalPoint('')
    }
  },
  computed: {
    lookupFocalPoint () { return this.$store.state.lookupFocalPoint[this.lookupType] },
    optionsLookupFunctionalLocation () {
      return this.lookupFncLocation.map(dm => {
        return {
          value: dm.Id,
          label: dm.Name,
          icon: 'account'
        }
      })
    },
    optionsLookupFocalPoint () {
      return this.lookupFocalPoint.map(dm => {
        return {
          value: dm.Id,
          label: dm.FirstName + ' ' + dm.LastName,
          icon: 'people'
        }
      })
    }
  },
  watch: {
    'emoc.MOC_Focal_Point_Name__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupFocalPoint(newVal)
      }
    }
  },
  methods: {
    filterLookupFocalPoint (searchParam) {
      this.$store.dispatch('receiveLookupFocalPoint', {lookupType: this.lookupType, sendParam: searchParam})
    }
  }
}
</script>


