<template>
  <div name="technical-review-by-moc-focal-point">
    <expandable-section-std title="Technical Review By MOC Focal point" ref="expandTaReviewMocFocal" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-12">
            <button id="btnConComId" class="slds-button slds-button_neutral" @click="callConsolidateComment">Consolidate comment</button>
            <button id="btnTaAllPosition" class="slds-button slds-button_neutral" @click="callViewTaChecklist">View TA Checklist</button>
            <button id="btnTaMocViewDocReq" class="slds-button slds-button_neutral" @click="callViewChecklist">View Document Require Checklist</button>
            <button class="slds-button slds-button_neutral" @click="btnFlowReturnToReviewStepConcernedTA">Return to review step by concerned TA</button>
          </div>
        </div>
        <div class="row">
          <div class="col-6">
            <checkbox-std
                  v-model="emoc.I_have_incorporated_comments__c"
                  label="I have incorporated comments and ready for next approval step."
                  :inline="true" disabled
              />
          </div>
          <div class="col-6">
            <lookups-std v-model="emoc.Action_by_MOC__c"
                         data-vv-as="Action_by_MOC__c"
                         data-vv-value-path="inputValue"
                         label="Action by"
                         placeholder="Search..."
                         :options="optionsLookupUser3()"
                         name="Action_by_MOC__c"
                         @search="filterLookupUser(arguments[0])"
                         icon="people"
                         disabled
                         loading>
            </lookups-std>
          </div>
        </div>
      </div>
    </expandable-section-std>
    <modal-std size="large" ref="refModalConsolidateComment"
               id="modalConsolidateCommentPopup">
      <template #header>
        <div style="text-align: left;"><span class="slds-card__header-link slds-text-heading_small">E-MOC Attachment Consolidate</span></div>
      </template>
      <template #content>
        <consolidate-comment-popup ref="refConsolidateComment"/>
      </template>
      <template #footer>
        <div style="text-align: center;">
          <button id="btnCallBackConComId" class="slds-button slds-button_neutral" @click="callBackConCom">Back</button>
        </div>
      </template>
    </modal-std>
    <!-- <modal-std size="large" ref="refModalDocReqChecklist"
               @close="afterClosePopup"
               id="docReqChecklistPopup">
      <template #header>
        <div style="text-align: left;"><span class="slds-card__header-link slds-text-heading_small">{{displayDocReq.view ? 'View document require checklist' : 'Document require checklist'}}</span></div>
      </template>
      <template #content>
        <doc-req-checklist-popup v-if="displayDocReq.create || displayDocReq.view"
                                :displayChecklist="displayDocReq"
                                ref="refDocReqChecklist"/>
      </template>
      <template #footer>
        <div style="text-align: center;">
          <button class="slds-button slds-button_neutral" @click="callBackDocReq">{{ displayDocReq.view ? 'Back' : 'Cancel' }}</button>
        </div>
      </template>
    </modal-std>
    <modal-std size="large" ref="refModalTaChecklist"
               @close="afterClosePopup"
               id="taChecklistPopup">
      <template #header>
        <div style="text-align: left;"><span class="slds-card__header-link slds-text-heading_small">{{displayTa.view ? 'View TA checklist' : 'TA checklist'}}</span></div>
      </template>
      <template #content>
        <ta-checklist-popup v-if="displayTa.create || displayTa.view"
                            :displayChecklist="displayTa"
                            ref="refTaChecklist"/>
      </template>
      <template #footer>
        <div style="text-align: center;">
          <button class="slds-button slds-button_neutral" @click="callBackTaChecklist">{{ displayTa.view ? 'Back' : 'Cancel' }}</button>
        </div>
      </template>
    </modal-std> -->
    
    <modal-after-save id="afterSaveTaChecklist"
                      @close="closePopupSuccess"
                      ref="popupAfterSave"/>
    <modal-waiting ref="popupLoader"/>
  </div>
</template>
<script>
import docReqChecklistPopup from './DocReqChecklistPopup.vue'
import taChecklistPopup from './TaChecklistPopup.vue'
import consolidateCommentPopup from './ConsolidateCommentPopup.vue'

export default {
  name: 'technical-review-by-moc-focal-point',
  props: [
    'emoc', 'displayDocReq', 'displayTa', 'displayConsolidate', 'setTaPositionSelected', 'setCallFrom'
  ],
  data () {
    return {
      resultSuccess: {},
      lookupType: 'technicalMoc'
    }
  },
  created () {
    if (this.emoc.Action_by_MOC__c !== '' && this.emoc.Action_by_MOC__c) {
      this.filterLookupUser(this.emoc.Action_by_MOC__c)
    }
  },
  computed: {
    lookupUser () { return this.$store.state.lookupUser }
  },
  watch: {
    'emoc.Action_by_MOC__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(newVal)
      }
    }
  },
  methods: {
    showPopupDocReq () {
      this.$refs.refModalDocReqChecklist.onOpen()
    },
    afterClosePopup () {
      // this.displayChecklist.create = false
      // this.displayChecklist.view = false
      this.displayDocReq.create = false
      this.displayDocReq.view = false
      this.displayTa.create = false
      this.displayTa.view = false
    },
    callBackDocReq () {
      this.$nextTick(() => {
        this.$refs.refModalDocReqChecklist.onClose()
      })
    },
    callViewChecklist () {
      this.displayDocReq.view = true
      // this.showPopupDocReq()
      this.setCallFrom('taReviewMocFocal')
      this.$store.dispatch('receiveDocReqChecklist', {Id: this.emoc.Id, DocType: 'Document Require Checklist'})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCloseDocReq')
        setTimeout(() => elements.focus(), 100)
      })
    },
    showPopupTaChecklist () {
      this.$refs.refModalTaChecklist.onOpen()
    },
    callBackTaChecklist () {
      this.$nextTick(() => {
        this.$refs.refModalTaChecklist.onClose()
      })
    },
    callViewTaChecklist () {
      this.displayTa.view = true
      this.setTaPositionSelected(0, 'allPosition')
      // this.showPopupTaChecklist()
      this.$store.dispatch('receiveTaChecklist', {Id: this.emoc.Id, Position__c: ''})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCloseTa')
        setTimeout(() => elements.focus(), 100)
      })
    },
    callConsolidateComment () {
      this.$refs.refModalConsolidateComment.onOpen()
      this.$store.dispatch('receiveConsolidateComment', {emocApplicationId: this.emoc.Id})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCallBackConComId')
        setTimeout(() => elements.focus(), 100)
      })
    },
    callBackConCom () {
      this.$nextTick(() => {
        this.$refs.refModalConsolidateComment.onClose()
        let elements = document.getElementById('btnConComId')
        setTimeout(() => elements.focus(), 100)
      })
    },
    optionsLookupUser3 () {
      if (typeof this.lookupUser[this.lookupType] !== 'undefined') {
        return this.lookupUser[this.lookupType].map(dm => {
          return {
            value: dm.Id,
            label: dm.FirstName + ' ' + dm.LastName,
            icon: 'people'
          }
        })
      } else {
        return []
      }
    },
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', {type: this.lookupType, sendParam: searchParam})
    },
    showPopupAfterSave () {
      this.$refs.popupAfterSave.onOpen()
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    btnFlowReturnToReviewStepConcernedTA () {
      let params = {
        emocAppId: this.emoc.Id
      }
      this.$store.dispatch('saveFlowReturnToReviewStepConcernedTA', params)
        .then(result => {
          if (result.statusSuccess) {
            this.showPopupAfterSave()
            this.resultSuccess = result
          }
        })
    }
  },
  components: {
    docReqChecklistPopup,
    taChecklistPopup,
    consolidateCommentPopup
  }
}
</script>


