<template>
  <div name="doc-req-checklist">
    <expandable-section-std title="Document Required Checklist" ref="expandDocReqChecklist" :closedOnStart="false">
      <div v-show="!statusRejectCancel" class="row">
        <div class="col-md-12" style="padding-bottom: 0px; margin-top: 10px;">
          <label>Document Required Checklist<abbr class="slds-required" title="required"> *</abbr></label>
        </div>
        <div class="col-md-6" style="margin-bottom: 10px;">
          <div class="button-doc-req-checklist">
            <button v-if="!(statusClosed || statusLessonLearn)" id="btnEditDocReq" class="slds-button slds-button_neutral btn-edit-doc-req" @click="callDocReqChecklist">Edit Document Require Checklist</button>
            <button v-show="emoc.Doc_Req_Result > 0" class="slds-button slds-button_neutral" @click="callViewChecklist">View Document Require Checklist</button>
          </div>
        </div>
        <div class="col-md-6">
          <!-- <label class="slds-form-element__label">Attachment Document Guideline</label><br> -->
          <button class="slds-button slds-button_neutral button-float2" @click="$refs.linkDocStorageGuide.click()">MOC Document List / Storage</button>
          <button class="slds-button slds-button_neutral button-float" @click="$refs.linkAttDocGuide.click()">MOC Document Guideline</button>
          <a hidden ref="linkAttDocGuide" :href="emoc.Document_Guideline_type__c" target="_blank">{{emoc.Document_Guideline_type__c}}</a>
          <a hidden ref="linkDocStorageGuide" :href="emoc.Document_Storage_Guideline__c" target="_blank">{{emoc.Document_Storage_Guideline__c}}</a>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>

export default {
  name: 'doc-req-checklist',
  props: [
    'emoc', 'statusRejectCancel', 'displayChecklist', 'setCallFrom', 'statusClosed', 'statusLessonLearn'
  ],
  data () {
    return {
      resultSuccess: {}
    }
  },
  methods: {
    callDocReqChecklist () {
      this.displayChecklist.create = true
      this.setCallFrom('docReqChecklist')
      this.$store.dispatch('receiveDocReqChecklist', {Id: this.emoc.Id, DocType: 'Document Require Checklist'})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCloseDocReq')
        setTimeout(() => elements.focus(), 100)
      })
    },
    callViewChecklist () {
      this.displayChecklist.view = true
      this.setCallFrom('docReqChecklist')
      this.$store.dispatch('receiveDocReqChecklist', {Id: this.emoc.Id, DocType: 'Document Require Checklist'})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCloseDocReq')
        setTimeout(() => elements.focus(), 100)
      })
    }
  }
}
</script>

<style scoped>
  /* .link-guideline {
    text-align: right;
  } */
  .button-doc-req-checklist {
    width: 490px;
  }
  @media only screen and (max-width: 960px) {
    .link-guideline {
      display: none;
    }
  }
  @media only screen and (max-width: 500px) {
    .button-doc-req-checklist {
      width: auto;
    }
    .btn-edit-doc-req {
      margin-bottom: 10px;
    }
    .slds-button.slds-button {
      margin-left: 0rem;
    }
  }
  .button-float {
    float: right;
    margin-right: 8px;
  }
  .button-float2 {
    float: right;
  }
</style>


