<template>
  <div name="pssr-category">
    <expandable-section-std title="Readiness Review Category" ref="expandPssrCategory" :closedOnStart="false">
      <div class="data-frame">
        <checkbox-std
            v-model="emoc.CATEGORY_1__c"
            label="PSSR CATEGORY-1"
            name="CATEGORY_1__c"
            :inline="true"
            disabled
        />
        <div v-if="emoc.CATEGORY_1__c">
          <div class="row">
            <div class="col-sm-6">
              <lookups-std v-model="emoc.Assigned_To_Category_1__c"
                          v-validate="'required'"
                          data-vv-as="Assigned To"
                          data-vv-value-path="inputValue"
                          label="Assigned To"
                          placeholder="Search..."
                          :options="optionsLookupFocalPoint"
                          :required="true"
                          :error="errors.has('Assigned_To_Category_1__c')"
                          name="Assigned_To_Category_1__c"
                          @search="filterLookupFocalPoint(arguments[0])"
                          icon="people"
                          loading
                          disabled >
                <template #error>
                  <span>{{ errors.first('Assigned_To_Category_1__c') }}</span>
                </template>
              </lookups-std>
            </div>
            <div class="col-sm-6">
              <input-text-std v-model="emoc.Employee_ID_Category_1__c" label="Employee ID" disabled></input-text-std>
            </div>
          </div>
          <div class="row">
            <div class="col-sm-6">
              <input-text-std v-model="emoc.Position_Category_1__c" label="Position" disabled></input-text-std>
            </div>
            <div class="col-sm-6">
              <button v-show="emoc.CATEGORY_1_Form__c !== ''" class="slds-button slds-button_neutral category-link" @click="$refs.linkCategory1Ref.click()">CATEGORY-1 Form</button>
              <!-- <label class="slds-form-element__label">CATEGORY-1 Form</label><br> -->
              <a hidden ref="linkCategory1Ref" :href="emoc.CATEGORY_1_Form__c" target="_blank">{{emoc.CATEGORY_1_Form__c}}</a>
            </div>
          </div>
        </div>
      </div>
      <div class="data-frame">
        <checkbox-std
            v-model="emoc.CATEGORY_2__c"
            label="PSSR CATEGORY-2"
            name="CATEGORY_2__c"
            :inline="true"
            disabled
        />
        <div v-if="emoc.CATEGORY_2__c">
          <div class="row">
            <div class="col-sm-6">
              <lookups-std v-model="emoc.Assigned_To_Category_2__c"
                          v-validate="'required'"
                          data-vv-as="Assigned To"
                          data-vv-value-path="inputValue"
                          label="Assigned To"
                          placeholder="Search..."
                          :options="optionsLookupFocalPoint"
                          :required="true"
                          :error="errors.has('Assigned_To_Category_2__c')"
                          name="Assigned_To_Category_2__c"
                          @search="filterLookupFocalPoint(arguments[0])"
                          icon="people"
                          loading
                          disabled >
                <template #error>
                  <span>{{ errors.first('Assigned_To_Category_2__c') }}</span>
                </template>
              </lookups-std>
            </div>
            <div class="col-sm-6">
              <input-text-std v-model="emoc.Employee_ID_Category_2__c" label="Employee ID" disabled></input-text-std>
            </div>
          </div>
          <div class="row">
            <div class="col-sm-6">
              <input-text-std v-model="emoc.Position_Category_2__c" label="Position" disabled></input-text-std>
            </div>
            <div class="col-sm-6">
              <button v-show="emoc.CATEGORY_2_Form__c !== ''" class="slds-button slds-button_neutral category-link" @click="$refs.linkCategory2Ref.click()">CATEGORY-2 Form</button>
              <!-- <label class="slds-form-element__label">CATEGORY-2 Form</label><br> -->
              <a hidden ref="linkCategory2Ref" :href="emoc.CATEGORY_2_Form__c" target="_blank">{{emoc.CATEGORY_2_Form__c}}</a>
            </div>
          </div>
        </div>
      </div>
      <div class="row" v-if="emoc.flgShowConfirmeAlreadyUploadPSSR">
        <div class="col-md-6">
          <checkbox-std
            v-model="emoc.I_have_confirmed_already_upload_PSSR__c"
            label="I have confirmed PSSR result already uploaded into this E-MOC"
            :inline="true"
            disabled
            @onChange="setActionBy(emoc.I_have_confirmed_already_upload_PSSR__c)"
          />
        </div>
        <div class="col-md-6">
          <lookups-std v-model="emoc.Action_by_PSSR__c"
                       label="Action by"
                       placeholder="Search..."
                       :options="optionsLookupUser()"
                       name="Action_by_PSSR__c"
                       @search="filterLookupUser(arguments[0])"
                       icon="people"
                       disabled
                       loading>
          </lookups-std>
        </div>
      </div>
    <!--   <div class="data-frame">
        <checkbox-std
            v-model="emoc.CATEGORY_3__c"
            label="CATEGORY-3"
            name="CATEGORY_3__c"
            :inline="true"
            disabled
        />
        <div v-if="emoc.CATEGORY_3__c">
          <div class="row">
            <div class="col-sm-6">
              <lookups-std v-model="emoc.Assigned_To_Category_3__c"
                          v-validate="'required'"
                          data-vv-as="Assigned To"
                          data-vv-value-path="inputValue"
                          label="Assigned To"
                          placeholder="Search..."
                          :options="optionsLookupFocalPoint"
                          :required="true"
                          :error="errors.has('Assigned_To_Category_3__c')"
                          name="Assigned_To_Category_3__c"
                          @search="filterLookupFocalPoint(arguments[0])"
                          icon="people"
                          loading
                          disabled >
                <template #error>
                  <span>{{ errors.first('Assigned_To_Category_3__c') }}</span>
                </template>
              </lookups-std>
            </div>
            <div class="col-sm-6">
              <input-text-std v-model="emoc.Employee_ID_Category_3__c" label="Employee ID" disabled></input-text-std>
            </div>
          </div>
          <div class="row">
            <div class="col-sm-6">
              <input-text-std v-model="emoc.Position_Category_3__c" label="Position" disabled></input-text-std>
            </div>
            <div class="col-sm-6">
              <button v-show="emoc.CATEGORY_3_Form__c !== ''" class="slds-button slds-button_neutral category-link" @click="$refs.linkCategory3Ref.click()">CATEGORY-3 Form</button>
              <a hidden ref="linkCategory3Ref" :href="emoc.CATEGORY_3_Form__c" target="_blank">{{emoc.CATEGORY_3_Form__c}}</a>
            </div>
          </div>
        </div>
      </div> -->
      <div class="data-frame">
        <checkbox-std
          v-model="emoc.QA_QC__c"
          label="QA/QC"
          name="QA_QC__c"
          :inline="true"
          disabled
        />
        <div v-if="emoc.QA_QC__c" class="row" style="padding-top: 15px;">
          <div v-if="emoc.flgShowConfirmeQaQc && !statusRejectCancel" class="col-12">
            <button v-if="!(statusClosed || statusLessonLearn)" id="btnEditQaQc" class="slds-button slds-button_neutral" @click="callQaQcChecklist">Edit QA/QC Checklist</button>
            <button v-show="emoc.QA_QC_Checklist_Result > 0" class="slds-button slds-button_neutral" @click="callViewChecklist">View QA/QC Checklist</button>
          </div>
          <div class="col-sm-6">
            <lookups-std v-model="emoc.Assigned_To_QA_QC__c"
                         v-validate="showSectionMocFocalPoint ? 'required' : ''"
                         data-vv-as="MOC Focal Point Name"
                         data-vv-value-path="inputValue"
                         label="MOC Focal Point Name"
                         placeholder="Search..."
                         :options="optionsLookupFocalPoint"
                         :required="true"
                         :error="errors.has('Assigned_To_QA_QC__c')"
                         name="Assigned_To_QA_QC__c"
                         @search="filterLookupFocalPoint(arguments[0])"
                         icon="people"
                         loading
                         disabled >
              <template #error>
                <span>{{ errors.first('Assigned_To_QA_QC__c') }}</span>
              </template>
            </lookups-std>
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Employee_ID_QA_QC__c" label="MOC Focal Point ID" disabled></input-text-std>
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Position_QA_QC__c" label="Position" disabled></input-text-std>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
import qaQcChecklistPopup from './QaQcChecklistPopup.vue'

export default {
  name: 'pssr-category',
  props: [
    'emoc', 'showSectionMocFocalPoint', 'statusRejectCancel', 'displayChecklist', 'statusClosed', 'statusLessonLearn'
  ],
  data () {
    return {
      lookupType: 'pssr_category',
      resultSuccess: {}
    }
  },
  created () {
    if (this.emoc.CATEGORY_1__c) {
      if (this.emoc.Assigned_To_Category_1__c !== '' && this.emoc.Assigned_To_Category_1__c) {
        this.filterLookupFocalPoint(this.emoc.Assigned_To_Category_1__c)
      } else {
        this.filterLookupFocalPoint('')
      }
    } else if (this.emoc.CATEGORY_2__c) {
      if (this.emoc.Assigned_To_Category_2__c !== '' && this.emoc.Assigned_To_Category_2__c) {
        this.filterLookupFocalPoint(this.emoc.Assigned_To_Category_2__c)
      } else {
        this.filterLookupFocalPoint('')
      }
    } else if (this.emoc.CATEGORY_3__c) {
      if (this.emoc.Assigned_To_Category_3__c !== '' && this.emoc.Assigned_To_Category_3__c) {
        this.filterLookupFocalPoint(this.emoc.Assigned_To_Category_3__c)
      } else {
        this.filterLookupFocalPoint('')
      }
    } else if (this.emoc.QA_QC__c) {
      if (this.emoc.Assigned_To_QA_QC__c !== '' && this.emoc.Assigned_To_QA_QC__c) {
        this.filterLookupFocalPoint(this.emoc.Assigned_To_QA_QC__c)
      } else {
        this.filterLookupFocalPoint('')
      }
    } else {
      this.filterLookupFocalPoint('')
    }
    if (this.emoc.Action_by_PSSR__c !== '' && this.emoc.Action_by_PSSR__c) {
      this.filterLookupUser(this.emoc.Action_by_PSSR__c)
    }
  },
  computed: {
    lookupFocalPoint () { return this.$store.state.lookupFocalPoint[this.lookupType] },
    optionsLookupFocalPoint () {
      return this.lookupFocalPoint.map(dm => {
        return {
          value: dm.Id,
          label: dm.FirstName + ' ' + dm.LastName,
          meta: dm.Employee_Id__c,
          icon: 'people'
        }
      })
    },
    mocFocalPointEditDisabled () {
      return this.emoc.Status_Common_Workflow__c !== 'Concept Approval'
    },
    lookupUser () { return this.$store.state.lookupUser }
  },
  watch: {
    'emoc.CATEGORY_1__c' (newVal) {
      if (newVal) {
        this.emoc.Assigned_To_Category_2__c = ''
        this.emoc.Assigned_To_Category_3__c = ''
        this.emoc.Assigned_To_QA_QC__c = ''
        this.$nextTick(() => {
          this.emoc.CATEGORY_2__c = false
          this.emoc.Employee_ID_Category_2__c = ''
          this.emoc.Position_Category_2__c = ''
          this.emoc.CATEGORY_3__c = false
          this.emoc.Employee_ID_Category_3__c = ''
          this.emoc.Position_Category_3__c = ''
          this.emoc.QA_QC__c = false
          this.emoc.Employee_ID_QA_QC__c = ''
          this.emoc.Position_QA_QC__c = ''
        })
      } else {
        this.filterLookupFocalPoint('')
      }
    },
    'emoc.CATEGORY_2__c' (newVal) {
      if (newVal) {
        this.emoc.Assigned_To_Category_1__c = ''
        this.emoc.Assigned_To_Category_3__c = ''
        this.emoc.Assigned_To_QA_QC__c = ''
        this.$nextTick(() => {
          this.emoc.CATEGORY_1__c = false
          this.emoc.Employee_ID_Category_1__c = ''
          this.emoc.Position_Category_1__c = ''
          this.emoc.CATEGORY_3__c = false
          this.emoc.Employee_ID_Category_3__c = ''
          this.emoc.Position_Category_3__c = ''
          this.emoc.QA_QC__c = false
          this.emoc.Employee_ID_QA_QC__c = ''
          this.emoc.Position_QA_QC__c = ''
        })
      } else {
        this.filterLookupFocalPoint('')
      }
    },
    'emoc.CATEGORY_3__c' (newVal) {
      if (newVal) {
        this.emoc.Assigned_To_Category_1__c = ''
        this.emoc.Assigned_To_Category_2__c = ''
        this.emoc.Assigned_To_QA_QC__c = ''
        this.$nextTick(() => {
          this.emoc.CATEGORY_1__c = false
          this.emoc.Employee_ID_Category_1__c = ''
          this.emoc.Position_Category_1__c = ''
          this.emoc.CATEGORY_2__c = false
          this.emoc.Employee_ID_Category_2__c = ''
          this.emoc.Position_Category_2__c = ''
          this.emoc.QA_QC__c = false
          this.emoc.Employee_ID_QA_QC__c = ''
          this.emoc.Position_QA_QC__c = ''
        })
      } else {
        this.filterLookupFocalPoint('')
      }
    },
    'emoc.QA_QC__c' (newVal) {
      if (newVal) {
        this.emoc.Assigned_To_Category_1__c = ''
        this.emoc.Assigned_To_Category_2__c = ''
        this.emoc.Assigned_To_Category_3__c = ''
        this.$nextTick(() => {
          this.emoc.CATEGORY_1__c = false
          this.emoc.Employee_ID_Category_1__c = ''
          this.emoc.Position_Category_1__c = ''
          this.emoc.CATEGORY_2__c = false
          this.emoc.Employee_ID_Category_2__c = ''
          this.emoc.Position_Category_2__c = ''
          this.emoc.CATEGORY_3__c = false
          this.emoc.Employee_ID_Category_3__c = ''
          this.emoc.Position_Category_3__c = ''
        })
      } else {
        this.filterLookupFocalPoint('')
      }
    },
    'emoc.Assigned_To_Category_1__c' (newVal) {
      if (newVal !== '' && newVal) {
        let defaultFomula = this.lookupFocalPoint.filter(df => {
          return df.Id === newVal
        })
        if (defaultFomula.length !== 0) {
          this.emoc.Position_Category_1__c = defaultFomula[0].INDICATOR__c
          this.emoc.Employee_ID_Category_1__c = defaultFomula[0].Employee_Id__c
        }
      } else {
        this.emoc.Position_Category_1__c = ''
        this.emoc.Employee_ID_Category_1__c = ''
        this.filterLookupFocalPoint('')
      }
    },
    'emoc.Assigned_To_Category_2__c' (newVal) {
      if (newVal !== '' && newVal) {
        let defaultFomula = this.lookupFocalPoint.filter(df => {
          return df.Id === newVal
        })
        if (defaultFomula.length !== 0) {
          this.emoc.Position_Category_2__c = defaultFomula[0].INDICATOR__c
          this.emoc.Employee_ID_Category_2__c = defaultFomula[0].Employee_Id__c
        }
      } else {
        this.emoc.Position_Category_2__c = ''
        this.emoc.Employee_ID_Category_2__c = ''
        this.filterLookupFocalPoint('')
      }
    },
    'emoc.Assigned_To_Category_3__c' (newVal) {
      if (newVal !== '' && newVal) {
        let defaultFomula = this.lookupFocalPoint.filter(df => {
          return df.Id === newVal
        })
        if (defaultFomula.length !== 0) {
          this.emoc.Position_Category_3__c = defaultFomula[0].INDICATOR__c
          this.emoc.Employee_ID_Category_3__c = defaultFomula[0].Employee_Id__c
        }
      } else {
        this.emoc.Position_Category_3__c = ''
        this.emoc.Employee_ID_Category_3__c = ''
        this.filterLookupFocalPoint('')
      }
    },
    'emoc.Assigned_To_QA_QC__c' (newVal) {
      if (newVal !== '' && newVal) {
        let defaultFomula = this.lookupFocalPoint.filter(df => {
          return df.Id === newVal
        })
        if (defaultFomula.length !== 0) {
          this.emoc.Position_QA_QC__c = defaultFomula[0].INDICATOR__c
          this.emoc.Employee_ID_QA_QC__c = defaultFomula[0].Employee_Id__c
        }
      } else {
        this.emoc.Position_QA_QC__c = ''
        this.emoc.Employee_ID_QA_QC__c = ''
        this.filterLookupFocalPoint('')
      }
    },
    'emoc.Action_by_PSSR__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(newVal)
      }
    }
  },
  methods: {
    filterLookupFocalPoint (searchParam) {
      this.$store.dispatch('receiveLookupFocalPoint', {lookupType: this.lookupType, sendParam: searchParam})
    },
    callQaQcChecklist () {
      this.displayChecklist.create = true
      this.$store.dispatch('receiveQaQcChecklist', {Id: this.emoc.Id, DocType: 'QA/QC checklist'})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCloseQaQc')
        setTimeout(() => elements.focus(), 100)
      })
    },
    callViewChecklist () {
      this.displayChecklist.view = true
      this.$store.dispatch('receiveQaQcChecklist', {Id: this.emoc.Id, DocType: 'QA/QC checklist'})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCloseQaQc')
        setTimeout(() => elements.focus(), 100)
      })
    },
    setActionBy (val) {
      this.emoc.Action_by_PSSR__c = (this.emoc.I_have_confirmed_already_upload_PSSR__c === true) ? this.emoc.User_Login : ''
      if (typeof this.emoc.Action_by_PSSR__c !== 'undefined' && this.emoc.Action_by_PSSR__c !== '') {
        this.filterLookupUser(this.emoc.User_Login)
      }
    },
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', {type: this.lookupType, sendParam: searchParam})
    },
    optionsLookupUser () {
      if (typeof this.lookupUser[this.lookupType] !== 'undefined') {
        return this.lookupUser[this.lookupType].map(dm => {
          return {
            value: dm.Id,
            label: dm.FirstName + ' ' + dm.LastName,
            icon: 'people'
          }
        })
      } else {
        return []
      }
    }
  },
  components: {
    qaQcChecklistPopup
  }
}
</script>

<style scoped>
  @media only screen and (min-width: 600px) {
    .category-link {
      top: 24px;
    }
  }
</style>


