<template>
  <div name="moc-type">
    <expandable-section-std title="MOC Type" ref="expandMOCType" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div v-show="emoc.Normal__c" class="col-md-6">
            <checkbox-std
              v-model="emoc.Normal__c"
              label="Normal"
              :inline="true"
              disabled
            />
          </div>
          <div v-show="emoc.Emergency__c" class="col-md-6">
            <checkbox-std
              v-model="emoc.Emergency__c"
              label="Emergency"
              :inline="true"
              disabled
            />
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'moc-type',
  props: [
    'emoc'
  ],
  watch: {
    'emoc.Normal__c' (newValue) {
      if (newValue) {
        this.emoc.Emergency__c = false
      }
    },
    'emoc.Emergency__c' (newValue) {
      if (newValue) {
        this.emoc.Normal__c = false
      }
    }
  }
}
</script>


