<template>
  <div name="hazard-screening-checklist">
    <expandable-section-std title="Hazard Screening Checklist" ref="expandHazardChecklist" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div v-show="!statusRejectCancel" class="col-8" style="margin-bottom: 10px;">
            <button v-if="!(statusClosed || statusLessonLearn)" id="btnEditHazard" class="slds-button slds-button_neutral" @click="callHazardChecklist"><abbr v-if="emoc.Hazard_Src_Result <= 0" class="slds-required" title="required">*</abbr>Edit Hazard Screening Checklist</button>
            <button v-show="emoc.Hazard_Src_Result > 0" class="slds-button slds-button_neutral" @click="callViewChecklist">View Hazard Screening Checklist</button>
          </div>

          <div v-show="emoc.Risk_Analysis_Guideline__c !== ''" class="col-sm-4" style="margin-bottom: 10px;">
            <button  class="slds-button slds-button_neutral" @click="$refs.linkRiskAnalysisGuideRef.click()">Risk Analysis Selection Guideline</button>
            <a hidden ref="linkRiskAnalysisGuideRef" :href="emoc.Risk_Analysis_Guideline__c" target="_blank">{{emoc.Risk_Analysis_Guideline__c}}</a>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
import hazardChecklistPopup from './HazardChecklistPopup.vue'

export default {
  name: 'hazard-screening-checklist',
  props: [
    'emoc', 'statusRejectCancel', 'displayChecklist', 'statusClosed', 'statusLessonLearn'
  ],
  data () {
    return {
      resultSuccess: {},
      flgCallSave: false
    }
  },
  methods: {
    callHazardChecklist () {
      this.displayChecklist.create = true
      this.$store.dispatch('receiveHazardChecklist', {Id: this.emoc.Id, DocType: 'Hazard Screen Checklist'})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCloseHazard')
        setTimeout(() => elements.focus(), 100)
      })
    },
    callViewChecklist () {
      this.displayChecklist.view = true
      this.$store.dispatch('receiveHazardChecklist', {Id: this.emoc.Id, DocType: 'Hazard Screen Checklist'})
      this.$nextTick(() => {
        let elements = document.getElementById('btnCloseHazard')
        setTimeout(() => elements.focus(), 100)
      })
    }
  },
  components: {
    hazardChecklistPopup
  }
}
</script>


