<template>
  <div name="document-require-checklist">
    <header class="slds-modal__header">
      <div style="text-align: left;">
        <span class="slds-card__header-link slds-text-heading_small">{{displayChecklist.view ? 'View document require checklist' : 'Document require checklist'}}</span>
        <button id="btnCloseDocReq" style="float: right; cursor: pointer;" title="Close" @click="onClose">
          <span class="slds-assistive-text">Close</span>
          <i class="fa fa-times" aria-hidden="true"></i>
        </button>
      </div>
    </header>
    <expandable-section-std  name="expandDocReqTypeName" v-for="(checklist, index) in remapFieldDocReq" :title="checklist.Type__c" :closedOnStart="index === 0 || displayChecklist.view ? false : true" :ref="'expandDocReqType' + index" :key="'expandDocReqType' + index">
      <div class="data-frame">
        <div class="scrollbar-table">
          <table id="document-require-checklist">
            <tr>
              <th width="5%">No.</th>
              <!-- <th width="10%">Checklist Type</th> -->
              <th width="30%">Detail</th>
              <th width="5%">Yes</th>
              <th width="30%">Remark</th>
              <th width="10%">Checklist REV.</th>
              <th width="10%">Last modified</th>
            </tr>
            <tr v-for="(question, idx2) in checklist.Question__c">
              <td style="text-align: center;">{{question.Question_No__c}}</td>
              <!-- <td><div class="type-list"><span :title="checklist.Type__c">{{checklist.Type__c}}</span></div></td> -->
              <td><div class="question-list"><span :title="question.Question__c">{{question.Question__c}}</span></div></td>
              <td style="text-align: center;">
                <checkbox-std v-model="question.Yes__c" :disabled="displayChecklist.view" :name="index + 'Yes__c' + idx2"/>
              </td>
              <td>
                <input-text-std style="min-width: 300px;" v-model="question.Remark__c" :disabled="displayChecklist.view"/>
              </td>
              <td><div class="column-revision">{{ displayChecklist.view ? checklist.Version__c : '' }}</div></td>
              <td><div class="column-last-modified">{{ displayChecklist.view ? checklist.CreatedBy.Name : '' }}</div></td>
            </tr>
          </table>
        </div>
      </div>
    </expandable-section-std>
    <footer class="slds-modal__footer">
      <div style="text-align: center;">
        <button class="slds-button slds-button_neutral" @click="onClose">{{ displayChecklist.view ? 'Back' : 'Cancel' }}</button>
        <button v-show="displayChecklist.create" class="slds-button slds-button_brand" @click="callSave">Save</button>
      </div>
    </footer>
    <modal-after-save id="afterSaveDocReqChecklist"
                      @close="closePopupSuccess"
                      ref="popupAfterSaveDocReqChkList"
                      :fixStyle="'height:700px;'" />

    <modal-waiting    ref="popupLoader" :fixStyle="'height:700px;'" />

  </div>
</template>
<script>
export default {
  name: 'document-require-checklist',
  props: [
    'emoc', 'displayChecklist', 'callDocReqFrom'
  ],
  data () {
    return {
      resultSuccess: {}
    }
  },
  computed: {
    docReqChecklist () { return this.$store.state.docReqChecklist },
    remapFieldDocReq () {
      let docSeq = 99999
      let remap = []
      if (this.displayChecklist.create) {
        this.docReqChecklist.map(dm => {
          if (dm.Sequence__c !== docSeq) {
            docSeq = dm.Sequence__c
            remap[dm.Sequence__c - 1] = {
              E_MOC_Application__c: dm.E_MOC_Application__c,
              Document__c: dm.Document__c,
              Type__c: dm.Type__c,
              Sequence__c: dm.Sequence__c,
              Version__c: dm.Version__c + 1,
              CreatedBy: dm.CreatedBy,
              Question__c: [{
                Question_No__c: dm.Question_No__c,
                Question__c: dm.Question__c,
                Yes__c: dm.Yes__c,
                No__c: dm.No__c,
                N_A__c: dm.N_A__c,
                Remark__c: dm.Remark__c
              }]
            }
          } else {
            remap[dm.Sequence__c - 1]['Question__c'].push({
              Question_No__c: dm.Question_No__c,
              Question__c: dm.Question__c,
              Yes__c: dm.Yes__c,
              No__c: dm.No__c,
              N_A__c: dm.N_A__c,
              Remark__c: dm.Remark__c
            })
          }
        })
      } else {
        let noItem = 0
        let noType = 0
        this.docReqChecklist.map(dm => {
          if (dm.Yes__c) {
            if (dm.Sequence__c !== docSeq) {
              noItem = 0
              docSeq = dm.Sequence__c
              remap[(++noType) - 1] = {
                E_MOC_Application__c: dm.E_MOC_Application__c,
                Document__c: dm.Document__c,
                Type__c: dm.Type__c,
                Sequence__c: dm.Sequence__c,
                Version__c: dm.Version__c,
                CreatedBy: dm.CreatedBy,
                Question__c: [{
                  Question_No__c: ++noItem,
                  Question__c: dm.Question__c,
                  Yes__c: dm.Yes__c,
                  No__c: dm.No__c,
                  N_A__c: dm.N_A__c,
                  Remark__c: dm.Remark__c
                }]
              }
            } else {
              remap[(noType) - 1]['Question__c'].push({
                Question_No__c: ++noItem,
                Question__c: dm.Question__c,
                Yes__c: dm.Yes__c,
                No__c: dm.No__c,
                N_A__c: dm.N_A__c,
                Remark__c: dm.Remark__c
              })
            }
          }
        })
      }
      return remap
    },
    genDataSave () {
      let remap = []
      this.remapFieldDocReq.map(dm => {
        dm.Question__c.map(dmq => {
          remap.push({
            E_MOC_Application__c: dm.E_MOC_Application__c,
            Document__c: dm.Document__c,
            Sequence__c: dm.Sequence__c,
            Type__c: dm.Type__c,
            Question_No__c: dmq.Question_No__c,
            Question__c: dmq.Question__c,
            Yes__c: dmq.Yes__c || false,
            No__c: false,
            N_A__c: false,
            Remark__c: dmq.Remark__c || '',
            Version__c: dm.Version__c
          })
        })
      })
      return remap
    }
  },
  methods: {
    onClose () {
      this.displayChecklist.create = false
      this.displayChecklist.view = false
      let elements = document.getElementById('btnEditDocReq')
      if (this.callDocReqFrom === 'taReviewMocFocal') {
        elements = document.getElementById('btnTaMocViewDocReq')
      }
      this.$nextTick(() => {
        setTimeout(() => elements.focus(), 100)
      })
    },
    callSave () {
      if (this.remapFieldDocReq.length > 0) {
        this.$nextTick(() => {
          let elements = document.getElementsByName(0 + 'Yes__c' + 0)
          this.$nextTick(() => {
            setTimeout(() => elements[0].focus(), 100)
          })
        })
      }
      this.showPopupLoader()
      this.$nextTick(() => {
        let elements = document.getElementById('loadingPageId')
        setTimeout(() => elements.focus(), 100)
      })
      this.$store.dispatch('saveDocReqChecklist', JSON.stringify(this.genDataSave))
        .then(result => {
          this.closePopupLoader()
          if (result.statusSuccess) {
            // this.callBackHazard()
            this.$nextTick(() => {
              // this.showPopupAfterSave()
              this.resultSuccess = result
            })
            this.showPopupAfterSave()
            this.$nextTick(() => {
              let elements = document.getElementById('btnafterSaveDocReqChecklist')
              setTimeout(() => elements.focus(), 100)
            })
          } else {
            console.log('Error', result)
          }
        })
    },
    showPopupAfterSave () {
      this.$refs.popupAfterSaveDocReqChkList.onOpen()
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
      // this.$store.dispatch('receiveEmocApplication', this.emoc.Id)
      // this.onClose()
    }
  }
}
</script>

<style scoped>
  #document-require-checklist td, #document-require-checklist th {
    border: 1px solid #ddd;
    padding: 5px;
  }
  #document-require-checklist th {
    background-color: #fafaf9;
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: center;
  }
  #document-require-checklist td>.type-list {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    min-width: 150px;
    max-width: 190px;
  }
  #document-require-checklist td>.question-list {
    overflow: hidden;
    text-overflow: ellipsis;
    /* white-space: nowrap; */
    min-width: 400px;
    max-width: 400px;
  }
  #document-require-checklist tr:hover {background-color: #f2f2f2;}
  #document-require-checklist .slds-form-element__label {display: none;}
  #document-require-checklist .slds-checkbox_faux {margin-right: 0px;}
  .scrollbar-table {
    overflow: auto;
  }
  .column-revision { min-width: 50px; text-align: center; }
  .column-last-modified { min-width: 200px; text-align: center; }
</style>


