<template>
  <div name="ta2">
    <expandable-section-std title="TA2" :closedOnStart="false" ref="expandTa2">
      <div class="row">
        <div class="col-sm-6">
          <div class="slds-border_bottom topic2">
            <label>TA Mandatory</label>
          </div>
          <div v-for="mappingDefault in taManMappingDefault" class="row">
            <div class="col-sm-6">
              <input-text-std v-model="mappingDefault.TA2_Indicator__c" label="Indicator / Name" disabled class="fontInput"></input-text-std>
            </div>
            <div class="col-sm-6">
              <input-text-std v-model="mappingDefault.TA2_Expert_Field__c" label="Expert field" disabled class="fontInput"></input-text-std>
            </div>
          </div>
        </div>
        <div class="col-sm-6">
          <div class="row">
            <div class="col-sm-6 padding-b0">
              <div class="slds-border_bottom topic2">
                <label>Additional TA2</label>
              </div>
            </div>
            <div class="col-sm-6 padding-b0">
              <button style="float: right; top: 4px;"  class="slds-button slds-button_neutral" @click="$refs.linkTaManGuide.click()">
                TA Mandatory Guideline
              </button>
              <a hidden ref="linkTaManGuide" :href="emoc.TA_Mandatory_Guideline__c" target="_blank">{{emoc.TA_Mandatory_Guideline__c}}</a>
            </div>
          </div>
          <div v-for="(mappingOptional, index) in taManMappingOptional" class="row margin-t0" :key="index">
            <div class="col-sm-6">
              <combobox-std
                v-model="mappingOptional.TA2_Indicator__c"
                :options="taManMasterOption(index)"
                label="Indicator / Name"
                disabled
                :classCustom="'font-size: 11px;'"
              />
            </div>
            <div class="col-sm-6">
              <input-text-std v-model="mappingOptional.TA2_Expert_Field__c" label="Expert field" disabled class="fontInput"></input-text-std>
            </div>
          </div>
          <!-- <div class="row">
            <div class="col-sm-6">
              <button class="slds-button slds-button_neutral" @click="clickAdd"><i class="fa fa-plus" aria-hidden="true"></i>&nbsp;&nbsp;Add</button>
            </div>
          </div> -->
        </div>
      </div>
      <!-- <div class="row">
        <div class="col-sm-6">
          <checkbox-std
            v-model="emoc.TA_Review_by_Technologist_Manager__c"
            label="TA selection confirmed by Technologist Manager"
            :inline="true"
            disabled
          />
        </div>
        <div class="col-sm-6">
          <input-text-std v-model="emoc.Action_by_Technologist__r.Name" label="Action by" disabled></input-text-std>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-6">
          <checkbox-std
            v-model="emoc.TA_Review_by_Operation_Manager__c"
            label="TA selection confirmed by Operation Manager"
            :inline="true"
            disabled
          />
        </div>
        <div class="col-sm-6">
          <input-text-std v-model="emoc.Action_by_Operation__r.Name" label="Action by" disabled></input-text-std>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-6">
          <checkbox-std
            v-model="emoc.TA_Review_by_Engineer_Manager__c"
            label="TA selection confirmed by Maintenance Manager"
            :inline="true"
            disabled
          />
        </div>
        <div class="col-sm-6">
          <input-text-std v-model="emoc.Action_by_Maintenance__r.Name" label="Action by" disabled></input-text-std>
        </div>
      </div> -->
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'ta2',
  props: ['emoc'],
  created () {
    this.$store.dispatch('receiveTaManMapping', this.emoc.Id)
    this.$store.dispatch('receiveTaManMaster', this.emoc.Id)
  },
  computed: {
    taManMaster () { return this.$store.state.taManMaster },
    taManMapping () { return this.$store.state.taManMapping },
    TaReviewbyTechnologistDisabled () { return this.$store.state.TaReviewbyTechnologistDisabled },
    TaReviewbyOperationDisabled () { return this.$store.state.TaReviewbyOperationDisabled },
    TaReviewbyEngineerDisabled () { return this.$store.state.TaReviewbyEngineerDisabled },
    taManMappingDefault () {
      return this.taManMapping.filter(df => {
        return df.Require__c === 'Require'
      })
    },
    taManMappingOptional () {
      return this.taManMapping.filter(df => {
        return df.Require__c === 'Optional'
      })
    }
  },
  methods: {
    taManMasterOption (index) {
      let retOption = this.taManMaster.filter(df => {
        return this.taManMappingOptional.map(dm => {
          return dm.TA2_Indicator__c
        }).indexOf(df.TA2_Indicator__c) === -1 || this.taManMappingOptional[index].TA2_Indicator__c === df.TA2_Indicator__c
      }).map(dm => {
        return {value: dm.TA2_Indicator__c, label: dm.TA2_Indicator__c}
      })
      return retOption
    },
    clickAdd () {
      this.taManMapping.push({Position__c: '', Require__c: 'Optional', TA_Level__c: '2'})
    }
  }
}
</script>
<style scoped>
  .fontInput {
    font-size: 11px;
  }
  .padding-b0 {
    padding-bottom: 0px;
  }
  .margin-t0 {
    margin-top: 0px;
  }
</style>


