<template>
  <div name="hazard-checklist-popup">
    <header class="slds-modal__header">
      <div style="text-align: left;">
        <span class="slds-card__header-link slds-text-heading_small">{{displayChecklist.view ? 'View Hazard Screening Checklist' : 'Hazard Screening Checklist'}}</span>
        <button id="btnCloseHazard" style="float: right; cursor: pointer;" title="Close" @click="onClose">
          <span class="slds-assistive-text">Close</span>
          <i class="fa fa-times" aria-hidden="true"></i>
        </button>
      </div>
    </header>
    <expandable-section-std v-for="(checklist, idx1) in hazardRemap" title="Hazard Screening Checklist" :closedOnStart="idx1 === 0 ? false : true" :ref="'expandHazard' + idx1" :key="'expandHazard' + idx1">
      <div class="data-frame">
        <div class="scrollbar-table">
          <table id="hazard-screening-checklist">
            <tr>
              <th width="5%">No.</th>
              <th width="35%">Issue</th>
              <th width="5%">Yes</th>
              <th width="5%">N.A</th>
              <th width="30%">Remark</th>
              <th width="10%">Checklist REV.</th>
              <th width="10%">Last modified</th>
            </tr>
            <tr v-for="(question, idx2) in checklist.Question__c">
              <td style="text-align: center;">{{question.Question_No__c}}</td>
              <td>
                <div class="question-list"><span :title="question.Question__c" style="overflow: hidden;">{{question.Question__c}}</span>
                  <div class="slds-form-element__icon help-text-icon " v-if="question.Help_text__c !== '' && typeof question.Help_text__c !== 'undefined'">
                    <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
                      <i class="fa fa-info-circle" aria-hidden="true"></i>
                      <span class="slds-assistive-text">Help</span>
                    </button>
                    <div class="slds-popover slds-popover_tooltip slds-nubbin_left help-text-display" role="tooltip" id="help" style="position:absolute;left:25px;text-align: left; z-index: -1;">
                      <div class="slds-popover__body">{{question.Help_text__c}}</div>
                    </div>
                  </div>
                </div>
              </td>

              <td style="text-align: center;">
                <checkbox-std v-model="question.Yes__c" :disabled="displayChecklist.view"
                              :name="idx1 + 'Yes__c' + idx2"
                              @onChange="clearAnotherCheckbox(idx1, idx2, 'Yes__c', question)"
                              :error="checkError(question)"/>
              </td>
              <td style="text-align: center;">
                <checkbox-std v-model="question.N_A__c" :disabled="displayChecklist.view"
                              @onChange="clearAnotherCheckbox(idx1, idx2, 'N_A__c', question)"
                              :error="checkError(question)"/>
              </td>
              <td>
                <input-text-std style="min-width: 300px;" v-model="question.Remark__c" :disabled="displayChecklist.view"/>
              </td>
              <td><div class="column-revision">{{ displayChecklist.view ? checklist.Version__c : '' }}</div></td>
              <td><div class="column-last-modified">{{ displayChecklist.view ? checklist.CreatedBy.Name : '' }}</div></td>
            </tr>
          </table>
        </div>
      </div>
    </expandable-section-std>
    <footer class="slds-modal__footer">
      <div style="text-align: center;">
        <button class="slds-button slds-button_neutral" @click="onClose">{{ displayChecklist.view ? 'Back' : 'Cancel' }}</button>
        <button v-show="displayChecklist.create" class="slds-button slds-button_brand" @click="callSave">Save</button>
      </div>
    </footer>

    <modal-after-save id="afterSaveHazardChecklist"
                      @close="closePopupSuccess"
                      ref="popupAfterSaveHazardChecklist"
                      :fixStyle="'height:700px;'" />

    <modal-waiting    ref="popupLoader" :fixStyle="'height:700px;'" />
  </div>
</template>
<script>
export default {
  name: 'hazard-checklist-popup',
  props: [
    'emoc', 'displayChecklist'
  ],
  data () {
    return {
      resultSuccess: {},
      hazardRemap: [],
      flgCallSave: false
    }
  },
  mounted () {
    this.remapFieldHazard()
  },
  computed: {
    hazardChecklist () { return this.$store.state.hazardChecklist },
    genDataSave () {
      let remap = []
      this.hazardRemap.map(dm => {
        dm.Question__c.map(dmq => {
          remap.push({
            E_MOC_Application__c: dm.E_MOC_Application__c,
            Document__c: dm.Document__c,
            Sequence__c: dm.Sequence__c,
            Type__c: dm.Type__c,
            Question_No__c: dmq.Question_No__c,
            Question__c: dmq.Question__c,
            Yes__c: dmq.Yes__c || false,
            No__c: false,
            N_A__c: dmq.N_A__c || false,
            Remark__c: dmq.Remark__c || '',
            Version__c: dm.Version__c,
            Help_text__c: dmq.Help_text__c
          })
        })
      })
      return remap
    }
  },
  watch: {
    hazardChecklist () {
      this.remapFieldHazard()
    }
  },
  methods: {
    remapFieldHazard () {
      let onlyOne = 1
      let docSeq = 99999
      let remap = []
      if (this.displayChecklist.create) {
        this.hazardChecklist.map(dm => {
          if (onlyOne !== docSeq) {
            docSeq = onlyOne
            remap[onlyOne - 1] = {
              E_MOC_Application__c: dm.E_MOC_Application__c,
              Document__c: dm.Document__c,
              Type__c: dm.Type__c,
              Sequence__c: onlyOne,
              Version__c: dm.Version__c + 1,
              CreatedBy: dm.CreatedBy,
              Question__c: [{
                Question_No__c: dm.Question_No__c,
                Question__c: dm.Question__c,
                Yes__c: dm.Yes__c,
                No__c: dm.No__c,
                N_A__c: dm.N_A__c,
                Remark__c: dm.Remark__c,
                Help_text__c: dm.Help_text__c
              }]
            }
          } else {
            remap[onlyOne - 1]['Question__c'].push({
              Question_No__c: dm.Question_No__c,
              Question__c: dm.Question__c,
              Yes__c: dm.Yes__c,
              No__c: dm.No__c,
              N_A__c: dm.N_A__c,
              Remark__c: dm.Remark__c,
              Help_text__c: dm.Help_text__c
            })
          }
        })
      } else {
        let noItem = 0
        let noType = 0
        this.hazardChecklist.map(dm => {
          if (dm.Yes__c || dm.N_A__c) {
            if (dm.Sequence__c !== docSeq) {
              noItem = 0
              docSeq = dm.Sequence__c
              remap[(++noType) - 1] = {
                E_MOC_Application__c: dm.E_MOC_Application__c,
                Document__c: dm.Document__c,
                Type__c: dm.Type__c,
                Sequence__c: dm.Sequence__c,
                Version__c: dm.Version__c,
                CreatedBy: dm.CreatedBy,
                Question__c: [{
                  Question_No__c: ++noItem,
                  Question__c: dm.Question__c,
                  Yes__c: dm.Yes__c,
                  No__c: dm.No__c,
                  N_A__c: dm.N_A__c,
                  Remark__c: dm.Remark__c,
                  Help_text__c: dm.Help_text__c
                }]
              }
            } else {
              remap[(noType) - 1]['Question__c'].push({
                Question_No__c: ++noItem,
                Question__c: dm.Question__c,
                Yes__c: dm.Yes__c,
                No__c: dm.No__c,
                N_A__c: dm.N_A__c,
                Remark__c: dm.Remark__c,
                Help_text__c: dm.Help_text__c
              })
            }
          }
        })
      }
      this.hazardRemap = remap
    },
    clearAnotherCheckbox (idx1, idx2, field, dataRow) {
      if (field === 'Yes__c' && dataRow[field]) {
        dataRow.N_A__c = false
      }
      if (field === 'N_A__c' && dataRow[field]) {
        dataRow.Yes__c = false
      }
    },
    checkError (dataRow) {
      if (this.flgCallSave) {
        if (dataRow.Yes__c || dataRow.N_A__c) {
          return false
        } else {
          return true
        }
      } else {
        return false
      }
    },
    showPopupAfterSave () {
      this.$refs.popupAfterSaveHazardChecklist.onOpen()
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    onClose () {
      this.displayChecklist.create = false
      this.displayChecklist.view = false
      let elements = document.getElementById('btnEditHazard')
      this.$nextTick(() => {
        setTimeout(() => elements.focus(), 100)
      })
    },
    callSave () {
      this.flgCallSave = true
      let hazardRemap = this.hazardRemap
      let isError = false
      if (hazardRemap.length > 0) {
        this.$nextTick(() => {
          let elements = document.getElementsByName(0 + 'Yes__c' + 0)
          this.$nextTick(() => {
            setTimeout(() => elements[0].focus(), 100)
          })
        })
      }
      for (var idx1 = 0; idx1 < hazardRemap.length; idx1++) {
        for (var idx2 = 0; idx2 < hazardRemap[idx1].Question__c.length; idx2++) {
          if (!hazardRemap[idx1].Question__c[idx2].Yes__c && !hazardRemap[idx1].Question__c[idx2].N_A__c) {
            isError = true
            if (!this.$refs['expandHazard' + idx1][0].isOpen) {
              this.$refs['expandHazard' + idx1][0].toggle()
            }
            this.$nextTick(() => {
              let elements = document.getElementsByName(idx1 + 'Yes__c' + idx2)
              this.$nextTick(() => {
                setTimeout(() => elements[0].focus(), 100)
              })
            })
            break
          }
        }
        if (isError) {
          break
        }
      }
      if (!isError) {
        this.showPopupLoader()
        this.$nextTick(() => {
          let elements = document.getElementById('loadingPageId')
          setTimeout(() => elements.focus(), 100)
        })
        this.$store.dispatch('saveHazardChecklist', JSON.stringify(this.genDataSave))
          .then(result => {
            this.closePopupLoader()
            if (result.statusSuccess) {
              this.$nextTick(() => {
                this.resultSuccess = result
              })
              this.showPopupAfterSave()
              this.$nextTick(() => {
                let elements = document.getElementById('btnafterSaveHazardChecklist')
                setTimeout(() => elements.focus(), 100)
              })
            }
          })
      }
    }
  }
}
</script>

<style scope>
  #hazard-screening-checklist td, #hazard-screening-checklist th {
    border: 1px solid #ddd;
    padding: 5px;
  }
  #hazard-screening-checklist th {
    background-color: #fafaf9;
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: center;
  }
  #hazard-screening-checklist td>.type-list {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    min-width: 150px;
    max-width: 190px;
  }
  #hazard-screening-checklist td>.question-list {
/*    overflow: hidden;*/
    text-overflow: ellipsis;
    /* white-space: nowrap; */
    min-width: 500px;
    max-width: 700px;
  }
  #hazard-screening-checklist tr:hover {background-color: #f2f2f2;}
  #hazard-screening-checklist .slds-form-element__label {display: none;}
  #hazard-screening-checklist .slds-checkbox_faux {margin-right: 0px;}
  .scrollbar-table {
    overflow: auto;
  }
  .column-revision { min-width: 91px; text-align: center; }
  .column-last-modified { min-width: 200px; text-align: center; }
</style>


